<template>
<div>
    <form-group>
        <name-condition slot="input" q-name="name" v-model="name"></name-condition>
        <mobile-condition slot="input" q-name="mobile" v-model="mobile"></mobile-condition>
        <uid-condition slot="input" q-name="uid" v-model="uid"></uid-condition>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <button type="button" class="btn btn-default btn-outline" @click="changeTime"><i class="icon-edit"></i> &nbsp;修改授权有效期</button>
    <div class="table-responsive">
      <table class="table table-bordered table-striped">
        <thead>
          <tr>
            <th>序号</th>
            <th>客户ID</th>
            <th>客户姓名</th>
            <th>身份证号码</th>
            <th>电话号码</th>
            <th>芝麻授权</th>
            <th>淘宝授权</th>
            <th>支付宝授权</th>
            <th>京东授权</th>
            <th>运营商授权</th>
<!--             <th>公积金授权</th>
<th>信用卡邮箱授权</th>
<th>同盾授权</th>
<th>社保授权</th>
<th>车险保单授权</th>
<th>学信网授权</th> -->
            <th>通讯录授权</th>
            <th>定位授权</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, index) in reDataList">
              <td>{{(pageNum - 1) * 8 + index + 1}}</td>
              <td>{{row.uid}}</td>
              <td>{{row.uname}}</td>
              <td>{{row.idcard}}</td>
              <td>{{row.mobile}}</td>

              <td>{{getDictValue(row.zmxy, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.taobao, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.alipay, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.jd, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.carrier, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.contact, dict.isOrNot)}}</td>
              <td>{{getDictValue(row.location, dict.isOrNot)}}</td>
              <td><a href="javascript:;" @click="showHis(row.uid)">查看授权更新记录</a></td>
          </tr>
        </tbody>
      </table>
      <!--暂无数据 start-->
      <div class="noData">
        {{errorInfo}}
      </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
/*
 * 授权管理
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import uidCondition from '../form-group/uid-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'

import hisAuth from './his-auth.vue'
import editAuth from './edit-auth.vue'


let pagesize = 8
export default {
    data () {
        return {'name': '', 'mobile': '', 'uid': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', dict}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.queryUserCreditTag
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'uname': this.name, 'mobile': this.mobile, 'uid': this.uid, 'offset': _offset, 'pagesize': pagesize}
        }

    }
    ,components: {queryBtnGroup, nameCondition, mobileCondition, uidCondition, formGroup, page}
    ,created () {
        this.doQuery()
        
    }
    ,methods: {
        showHis (uid) {
          this.$router.push({name: 'authHis', query: {'uid': uid}})
          
            
        } 
        ,changeTime () {
            eventHub.$emit('open-modal', editAuth)
        }
    }
    ,mixins: [mixins]
}
</script>


