<style lang="scss">
    @import "./order-detail.scss";
</style>
<template>
    <div class="detail-info ">
        <ul class="nav nav-tabs col-xs-12">
            <li v-for="(item, index) in tabList" :class="{active:item.iscur}" @click="setCur(index)"><a>{{item.name}}</a></li>
        </ul>
        <a href="javascript:;" class="blue backBtn" @click="goBack">返回上一页</a>
        <component :is="checkinfo" :id="id" :parentId="parentId" :routeId="routeId"></component>
    </div>
</template>
<script>
    /*
     * 详情页面
     */
    import action from '../../action/interface.js'
    import mixins from '../mixin.js'
    import Vue from 'vue'

    import outInfo from './out-info.vue'
    import basicInfo from './basic-info.vue'
    import MessageInfo from './message-info.vue'
    import MessageAddress from './message-address.vue'
    import trail from './trail.vue'
    import customerManagement from '../customer-management/customer-management.vue'


    export default {
        data() {
            return {
                'id': '', 'routeId': this.$route.query.routeId, 'parentId': this.$route.query.parentId, 'checkinfo': 'basicInfo',
                'tabList': [
                    { name: '基本信息', iscur: true, id: 'basicInfo' },
                    { name: '外部信息', iscur: false, id: 'outInfo' },
                    { name: '通讯录信息', iscur: false, id: 'MessageInfo' },
                    { name: '地址信息', iscur: false, id: 'MessageAddress' }]
            }
        }
        , components: { outInfo, basicInfo, trail, MessageInfo, MessageAddress, customerManagement }
        , mixins: [mixins]
        , activated() {
            this.id = this.$route.query.id;
            this.setCur(0)
        }
        , methods: {
            setCur: function (index) {
                this.tabList.map(function (v, i) {
                    i == index ? v.iscur = true : v.iscur = false;
                });
                this.checkinfo = this.tabList[index].id;
                this.id = this.$route.query.id;
                this.$emit('set-cur')
            }
            , goBack() { //返回
                this.$router.go(-1)
            }
        }
    }
</script>


