<template>
 <div class="infoTab">
    <div class="title col-xs-12">审核意见</div>
    <div class="formInfo"  v-for="item in reDataList">
      <div class="col-xs-12 clearfix">
        <div class="col-xs-6">
          <label>{{labelId}}意见：</label>
          <span v-if="item.status == 1">通过</span>
          <span v-else>未通过</span>
        </div>
      </div>
      <div class="col-xs-12 clearfix areaTxt">
        <label class="fl">{{labelId}}结论：</label>
        <span>{{item.detail}}</span>
      </div>
    </div>
    <div class="noData tl">
      {{errorInfo}}
    </div>
  </div>

</template>

<script>
/*
 * 查看审核意见
 */
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import loanInfo from '../check-info/loan-info.vue'
export default {
        props: ["id"]
        ,components: {loanInfo}
        ,data () {
            return {'reDataList': '','errorInfo': '','labelId':''}
        }
        ,computed: {
           'qParams': function () {
              let routeId = this.$route.query.routeId
              let _type
              if(routeId==2){
                  _type = 1
                  this.labelId = '初审'
              }else if(routeId==4){
                  _type = 2
                  this.labelId = '复核'
              }
              return {'parentId':this.$route.query.parentId,'type':_type}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.process
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>


