<template>
    <div>
        <form-group>
            <name-condition slot="input" q-name="uname" v-model="uname"></name-condition>
            <mobile-condition slot="input" q-name="mobile" v-model="mobile"></mobile-condition>
            <div class="form-group col-md-3" slot="input">
                <label class="control-label col-md-4 whp-n">产品类型：</label>
                <div class="col-md-8">
                    <select class="form-control" v-model="pid">
                        <option value="">全部</option>
                        <option value="1">随想贷</option>
                        <option value="3">车贷</option>
                        <option value="2">微米贷</option>
                        <option value="4">多米贷</option>
                    </select>
                </div>
            </div>
            <query-btn-group slot="btn"></query-btn-group>
        </form-group>
         <button type="button" class="btn btn-default btn-outline" @click="changeTime"><i class="icon-edit"></i> &nbsp;修改额度有效期</button> 
        <div class="table-responsive">
          <table class="table table-bordered table-striped">
            <thead>
            <tr>
              <th><input type="checkbox" v-model="checkAll"/></th>
              <th>序号</th>
              <th>客户姓名</th>
              <th>客户ID</th>
              <th>身份证号码</th>
              <th>手机号码</th>
              <th>是否白名单</th>
              <th>产品类型</th>
              <th>总额度（元）</th>
              <th>额度生效日期</th>
              <th>额度失效日期</th>
              <th>额度有效期（天）</th>
              <th>使用中额度（元）</th>
              <th>可用额度（元）</th>
              <th>期限与利率</th>
              <th>操作</th>
            </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList" @click="clickItem(item.uid,item.iswhite)">
                  <td><input type="checkbox" @click="checkbox(item.uid,item.iswhite)" :value="item.uid" :id="item.uid" v-model="uidList"/></td>
                  <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                  <td>{{item.uname}}</td>
                  <td>{{item.uid}}</td>
                  <td>{{item.idcard}}</td>
                  <td>{{item.mobile}}</td>
                  <td><span v-if="item.iswhite == 1">是</span><span v-else>否</span></td>
                  <td>{{item.productname}}</td>
                  <td>{{fMoney(item.creditmoney)}}</td>
                  <td>{{fDate(item.validdate,'yyyy-MM-dd')}}</td>
                  <td>{{fDate(item.invaliddate,'yyyy-MM-dd')}}</td>
                  <td>{{item.validterm}}</td>
                  <td>{{fMoney(item.usecreditmoney)}}</td>
                  <td>{{fMoney(item.validcreditmoney)}}</td>
                  <td><a href="javascript:;" @click="showDetail(item.interestterminfos,item.uname,item.productname)">详情</a></td>
                  <td>
                    <a v-if="item.productname != '微米贷'" href="javascript:;" @click="changeCreditMoney(item.uid,item.id,item.uname,item.creditmoney,item.iswhite)">修改额度</a><span  v-if="item.productname != '微米贷'">&nbsp;|&nbsp;</span><a href="javascript:;" @click="goHistory(item.uid)">历史额度</a>
                  </td>
                </tr>
            </tbody>
          </table>
          <!--暂无数据 start-->
          <div class="noData">
            {{errorInfo}}
          </div>
          <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
    </div>

</template>
<script>
/*
 * 授信管理主页面
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'

import changeTime from './change-time.vue'
import changeCreditMoney from './change-creditmoney.vue'
import queryhistory from './history.vue'
import detailCredit from './show-detail.vue'

var pagesize = 8
export default {
    data () {
        //uidList: 保存选择的用户ID 
        return {'uname': '', 'mobile': '', 'pageNum': 1, 'total': 0, 'uidList': [], 'reDataList': [], 'errorInfo': '', 'checkAll': false, dict,'pid':''}
    }
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'uname': this.uname, 'mobile': this.mobile, 'offset': _offset, 'pagesize': pagesize,'pid':this.pid}
        }
        , 'url': function () { //请求地址
            return action.rootPath + action.interface.creditQuery
        }
    }
    ,components: {queryBtnGroup, nameCondition, mobileCondition, formGroup, page, changeTime, queryhistory,changeCreditMoney,detailCredit}
    ,mixins: [mixins]
    ,created () {
        this.doQuery()
    }
    ,watch: {
        'checkAll': function (newV, old) {
            //如果全选 则uidList列表所有值
            if (newV) {
                let vm = this
                vm.reDataList.forEach(function (item) {
                        vm.uidList.push(item.uid)                
                })                
            } else { //如果没有，则uidList置空
                this.uidList = []
            }
        }
    }
    ,methods: {
        //根据uidList，修改有效期
        changeTime () {
            let uids = this.uidList.join(',')
            if(uids === '' || uids == undefined){
                eventHub.$emit('show-alert', '请选择至少一个客户')   
            }else{
                //打开弹窗，并传递参数
                eventHub.$emit('open-modal', changeTime, {'uids': uids});
            }
        }
        //显示利率详情
        ,showDetail (list,uname,productname) {
            eventHub.$emit('open-modal', detailCredit, {'list': list,'uname':uname,'productname':productname})
        }
        //修改总额度
        ,changeCreditMoney (uid,id,uname,creditmoney,iswhite) {
           // if(iswhite == 1){
                eventHub.$emit('open-modal', changeCreditMoney, {'uid':uid,'id': id,'uname':uname,'creditmoney':creditmoney}); 
            //}else{
                //eventHub.$emit('show-alert', '非白名单不能更新')  
           // }
        }
        //打开历史授信
        ,goHistory (id) {
            this.$router.push({name: 'creditHistory', query: {uid: id}})            
        }
        ,checkbox (uid ,iswhite) {
            if (uid == '' || uid == undefined ) return 
            let vm = this, _list = this.uidList
            let hasUid = false
            _list = _list.filter(function (item) {
                //如果uid已经被选中 
                if (uid == item ) {
                    hasUid = true
                    return false
                } else {
                    return true
                }           
            })
            //如果不存在，则插入uid
            if (!hasUid) { _list.push(uid) }
            vm.uidList = _list;  
        }
        ,clickItem (uid ,iswhite) {
            // if(this.node){

            // }
            if (uid == '' || uid == undefined ) return 
            let vm = this, _list = this.uidList
            let hasUid = false
            _list = _list.filter(function (item) {
                //如果uid已经被选中 
                if (uid == item ) {
                    hasUid = true
                    return false
                } else {
                    return true
                }           
            })
            //如果不存在，则插入uid
            if (!hasUid) { _list.push(uid) }
            vm.uidList = _list;           
        } 
    }
    ,beforeDestroy () {
        //注销组件前，关闭所有弹窗
        eventHub.$emit('close-all-modal')
    }
}
</script>


