<style lang="scss">
    @import './order-detail.scss';
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">OCR识别结果</h4>
        </div>
        <div class="modal-body ocrinfo">
            <form action="#" method="post" id="deleteWhite" class="regForm">
                <div class="col-xs-12 form-inline group-left validateModal loan-alert">
                    <div class="col-xs-12 clearfix">
                        <p>
                            <label class="fl">被编辑：</label>
                            <span v-if="modalProps.edited || modalProps.edited == 0">{{modalProps.edited*100 || 0}}%</span>
                        </p>
                    </div>
                    <div class="col-xs-12 clearfix">
                        <p>
                            <label class="fl"> 复印件：</label>
                            <span v-if="modalProps.photocopy || modalProps.photocopy == 0">{{modalProps.photocopy*100}}%</span>
                        </p>
                    </div>
                    <div class="col-xs-12 clearfix">
                        <p>
                            <label class="fl">屏幕翻拍：</label>
                            <span v-if="modalProps.screen || modalProps.screen == 0">{{modalProps.screen*100}}%</span>
                        </p>
                    </div>
                    <div class="col-xs-12 clearfix">
                        <p>
                            <label class="fl">临时身份证：</label>
                            <span v-if="modalProps.tempidphoto || modalProps.tempidphoto == 0">{{modalProps.tempidphoto*100}}%</span>
                        </p>
                    </div>
                    <div class="col-xs-12 clearfix">
                        <p>
                            <label class="fl">真实身份证：</label>
                            <span v-if="modalProps.idphoto || modalProps.idphoto == 0">{{modalProps.idphoto*100}}%</span>
                        </p>
                    </div>
                </div>
            </form>
            </div>
            <div class="modal-footer" style="margin-top:0;text-align: center;">
                 <span>
                    <button type="button" class="btn btn-default" @click="goBack">关闭</button>
                </span>
            </div>
        </div>
        <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * OCR识别结果
 * params: {modalProps: {id: 记录id}}
 */
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


    export default {
        props: ['modalProps']
        ,data () {
            return {'uname': '', 'idcard': '', 'mobile': '', 'remark': '','dict': dict}
        }
        ,components: {}

        ,created () {
        }
        ,mounted () {
            eventHub.$on('go-back', this.goBack)
        }
        ,methods: {
            close () {
                eventHub.$emit('close-modal')
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>



