
<template>
 <div id="container">
    <loan-info :id="id"></loan-info>
    <query-log :parentId="parentId" v-if="routeId==3||routeId==4"></query-log>
    <show-trail v-if="routeId==2||routeId==4"></show-trail>
    <submit-trail v-if="routeId==1||routeId==3"></submit-trail>
</div>
</template>

<script>
/*
 * 审核/复核页面
 */
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import Vue from 'vue'
import loanInfo from '../check-info/loan-info.vue'
import queryLog from '../check-info/query-log.vue'
import showTrail from '../check-info/show-trail.vue'
import submitTrail from '../check-info/submit-trail.vue'

export default {
        data () {
        return {'routeId':this.$route.query.routeId,'detail':'','parentId':this.$route.query.parentId,}
        }
        ,props: ["id"]
        ,components: {loanInfo, queryLog, showTrail, submitTrail}
        ,mixins: [mixins]
        }
        
</script>


