<template>
  <div class="infoTable">
        <div class="form-group clearfix" >
            <label class="control-label">
              查询日期：
            </label>
            <select class="input-sm input-normal"  id="year" v-model="selectYear">
              <option v-for="year in years" :value="year">
                {{year}}
              </option>
            </select>
            <select class="input-sm input-normal"  id="month" v-model="selectMonth">
              <option v-for="month in months" :value="month">
                {{month}}
              </option>
            </select>
            <a type="button" class="btn btn-default btn-sm btn-outline ml30" href="javascript:;" @click="doSearch"><i class="icon-search"></i>&nbsp;查询</a>
        </div>
            <table class="table table-bordered mt10">
              <thead>
              <tr>
                <th>通信方式</th>
                <th>对方号码</th>
                <th>开始时间</th>
                <th>通话时长</th>
                <th>通信地点</th>
                <th>通信类型</th>
                <th>套餐优惠</th>
                <th>新通话</th>
                <th>实时通讯费</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for='item in reDataList' v-if="reDataList">
                <td>{{item.locationtype}}</td>
                <td>{{item.peernumber}}</td>
                <td>{{item.time}}</td>
                <td>{{timeSecond(item.durationinsecond)}}</td>
                <td>{{item.location}}</td>
                <td>{{item.locationtype}}</td>
                <td></td>
                <td>{{item.name}}</td>
                <td>{{item.fee}}</td>
              </tr>
              </tbody>
            </table>
            <div class="noData">
                {{errorInfo}}
            </div>
            <page :page-info="pageInfo" :total="total"></page>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import Vue from 'vue'
import page from '../basic/page/page.vue'

import dateCondition from '../form-group/date-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'

var pagesize = 10;
export default {
        props: ["id"]
        ,components:{dateCondition,queryBtnGroup,page}
        ,data () {
            return {'reDataList': '','errorInfo': '',"billMonth":'','pageNum': 1, 'total': 0,
            'years':['2013','2014','2015','2016','2017','2018'],
            'months':['01','02','03','04','05','06','07','08','09','10','11','12'],
            'selectYear':'','selectMonth':''}
        }
        ,computed: {
           //分页用
           'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
           }
           ,'qParams': function () {
              let _offset = this.cptOffset(this.pageNum, pagesize)
              return {'id':this.id,"billMonth":this.selectYear+"-"+this.selectMonth,'offset': _offset, 'pageSize': pagesize}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.yysCallog
           }
       }
       ,mixins: [mixins]
       ,created () {
         var mydate = new Date();
         this.selectYear = mydate.getFullYear() ;
         this.selectMonth = mydate.getMonth()+1 ;
         this.doQuery();
         //监听go-page事件
        eventHub.$on('go-page', this.goPage);
       }
       ,methods :{
         doSearch(){
           this.pageInfo.currentpage = 1;
           this.pageNum = 1;
           this.doQuery();
         }
       }
        ,destroyed (){
          eventHub.$off('go-page', this.goPage);
        }
  }
</script>



