<style>
.modal-prd {

}
.regForm .form-group {
    margin-bottom: 20px;
    position: relative;
}

.input-lsm{
  width:80px;
  height: 30px;
  line-height: 30px;
  padding: 0 12px;
}

.rate-item{
  position: absolute;
  right: 210px;
  top:0;
}
.rate-item > .control-label {
    width: 50px; 
}
.rate-limit > select {
    width: 65px;
}
.mngrate-item{
  position: absolute;
  right:0;
  top:0;
}
.prod-delete{
   position: absolute;
   right: -20px;
   top: 8px;
}
.prod-delete i{
    font-size: 15px;
    color: red;
}
.mngrate-item > .control-label {
    width: 60px; 
}
.mngrate-limit > select {
    width: 65px;
}
.note{
  height:100px;
  width:220px;
}
.little-label {
    width: 60px;
    height: 30px;
    line-height: 30px;
}
.modal-footer {
    text-align: center;
}
.pl65 .valid-error{
    padding-left:65px!important;
}
</style>
<template>
    <div class="modal-content modal-prd">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">新增产品</h4>
        </div>
        <div class="modal-body">
            <form id="addProd" class="regForm">
                <div class="col-xs-12 form-inline">
                    <div class="form-group col-xs-12">
                        <label class="control-label">
                            产品类型：
                        </label>
                        <input class="form-control input-sm input-normal" name="name" required placeholder="请填写产品名称" v-model="name" type="text">
                        <span class="red" style="color: #b94a48;">（微米贷产品利率及管理费无法生效）</span>
                    </div>
                    <div class="form-group col-xs-12" v-for="(item,index) in productrateinfos">
                        <div class="rate-limit">
                            <label class="control-label">
                                贷款期限：
                            </label>
                            <input class="form-control input-lsm" v-model="item.term" :name="'term_'+index" type="text">
                            <select class="form-control input-lsm" v-model="item.termtype">
                              <option value="0">天</option>
                              <option value="1">月</option>
                            </select>
                        </div>
                        <div class="rate-item pl55">
                            <label class="control-label">
                                利率：
                            </label>
                            <input class="form-control input-lsm" v-model="item.interest" type="text" :name="'interest_'+index" required>
                            <span>%</span>
                        </div>
                        <div class="mngrate-item pl65">
                            <label class="control-label">
                                管理费：
                            </label>
                            <input class="form-control input-lsm" v-model="item.mngrate" type="text" :name="'mngrate_'+index" required>
                            <span>%</span>
                        </div>
                        <div class="prod-delete"><a href="javascript:;" @click="removeLimit(index,item.id)"><i class="icon-minus-sign red"></i></a>
                        </div>
                    </div>
                    <div class="form-group col-xs-12 center-block">
                        <button type="button" class="btn btn-default btn-sm pull-right" @click="addLimit">
                        <span class="glyphicon glyphicon-plus"></span>新增贷款期限
                        </button>
                    </div>
                    <div class="form-group col-xs-12">
                        <label class="control-label">
                            罚息利率：
                        </label>
                        <input class="form-control input-lsm" name="penaltyinterest" required v-model="penaltyinterest" type="text">
                        <span>%</span>
                    </div>
                    <div class="form-group col-xs-12">
                        <label class="control-label">
                            最高贷款额度：
                        </label>
                        <input class="form-control input-normal input-money" name="maxmoney" required v-model="maxmoney" type="text">
                        <span>元</span>
                    </div>
                    <div class="form-group col-xs-12">
                        <label class="control-label">
                            最低贷款额度：
                        </label>
                        <input class="form-control input-normal input-money" v-model="minmoney" name="minmoney" required type="text">
                        <span>元</span>
                    </div>
                    <div class="form-group col-xs-12">
                        <label>备注：</label>
                        <textarea class="note" name="remark" required v-model="remark"></textarea>
                    </div>
                </div>
            </form>
        </div>
        <div class="modal-footer">
            <save-back-btn></save-back-btn>
        </div>
    </div>
          <!-- /.modal-content -->
</template>
<script>
/*
 * 新增产品
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'

import saveBackBtn from '../query-btn-group/save-back-btn.vue'

var url = action.rootPath + action.interface.prodAdd
var initRate = {'termtype': '0','term': '0', 'interest': '0', 'mngrate': '0'}
    export default {
        components: {saveBackBtn}
        ,data () {
            return {'name': '产品名称', 'penaltyinterest': '', 'maxmoney': '', 'minmoney': '', 'productrateinfos': [initRate], 'remark': ''}
        }
        ,computed: {
            'params': function () {
                let vm = this
                //把productrateinfos中的利率interest转化为浮点
                let _productrateinfos = JSON.stringify(this.productrateinfos)
                let _rateList = JSON.parse(_productrateinfos).map(function(item){
                    item.interest = vm.percentToFloat(item.interest)
                    item.mngrate = vm.percentToFloat(item.mngrate)
                    return item
                })
                return {'name': this.name, 'penaltyinterest': this.percentToFloat(this.penaltyinterest), 'maxmoney': this.maxmoney, 'minmoney': this.minmoney, 'productrateinfos': JSON.stringify(_rateList), 'remark': this.remark}
            }
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
            let rulesList = {
                    'maxmoney': {
                        'number': true
                        ,'min': 0
                    }
                    ,'minmoney': {
                        'number': true
                        ,'min': 0
                    }
                    ,'remark': {
                        required:true
                    }
                    ,'penaltyinterest': {
                        'number': true
                        ,'min': 0
                        ,'max': 100
                    }
                }
            for(var i=0;i<10;i++){
                rulesList['term_'+i] = {
                        'number': true
                        ,'min': 0                      
                }
                rulesList['interest_'+i] = {
                        'number': true
                        ,'min': 0
                        ,'max': 100                          
                }
                rulesList['mngrate_'+i] = {
                        'number': true
                        ,'min': 0
                        ,'max': 100                        
                }
            }    
            $('#addProd').validate({
                rules: rulesList
            })             
        }
        ,methods: {
            //增加利率项
            addLimit () {
                let _limit = {'termtype': '0','term': '0', 'interest': '0', 'mngrate': '0'}
                this.productrateinfos.push(_limit)
            }
            //删除利率项
            ,removeLimit (index,id) {
                this.productrateinfos.splice(index,1);
            }
            //保存
            ,doSave () {
                if ($('#addProd').valid()){
                    if(Number(this.maxmoney) >= Number(this.minmoney)){
                        let _params = this.params
                        this.post(url, _params, function (res) {
                            if (res.errorcode == 0) {
                                eventHub.$emit('show-alert', '保存成功！')
                            } else {
                                eventHub.$emit('show-alert', res.message)
                            }
                        })   
                    }else{
                        //$(".input-money").next().append('<div id="minmoney-error" class="valid-error">最高贷款额度不能小于最低贷款额度</div>')
                        alert("最高贷款额度不能小于最低贷款额度")
                    }
                                     
                }

            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]

    }
</script>


