<template>
<div>
    <form-group>
        <name-condition slot="input" v-model="uname"></name-condition>
        <mobile-condition slot="input" v-model="mobile"></mobile-condition>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">贷款编号</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="loanNo"/>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4 whp-n">逾期开始日</label>
            <div class="col-md-8">
                <date-condition q-name="overdueStartTime" q-class="form-control" v-model="overdueStartTime"></date-condition>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">状态</label>
            <div class="col-md-8">
                <select class="form-control" v-model="status">
                    <option value="">全部</option>
                    <option value="0">未结清</option>
                    <option value="1">已结清</option>
                </select>
            </div>

        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4 whp-n">产品类型：</label>
            <div class="col-md-8">
                <select class="form-control" v-model="pid">
                    <option value="">全部</option>
                    <option value="1">随想贷</option>
                    <option value="3">车贷</option>
                    <option value="2">微米贷</option>
                    <option value="4">多米贷</option>
                </select>
            </div>
        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <td></td>
                    <td>产品类型</td>
                    <td>贷款编号</td>
                    <td>客户姓名</td>
                    <td>手机号码</td>
                    <td>身份证号</td>
                    <td>逾期开始日</td>
                    <td>逾期结束日</td>
                    <td>逾期天数</td>
                    <td>逾期本息(元)</td>
                    <td>已还逾期本息（元）</td>
                    <td>未还逾期本息（元）</td>
                    <td>已还罚息（元）</td>
                    <td>未还罚息（元）</td>
                    <td>当前状态</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{item.productname}}</td>
                    <td>{{item.loanno}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.mobile}}</td>
                    <td>{{item.idcard}}</td>
                    <td>{{fDate(item.starttime,'yyyy-MM-dd')}}</td>
                    <td>{{fDate(item.endtime,'yyyy-MM-dd')}}</td>
                    <td>{{item.day}}</td>
                    <td>{{fMoney(item.topaymoney)}}</td>
                    <td>{{fMoney(item.payedmoney)}}</td>
                    <td>{{fMoney(item.resttopaymoney)}}</td>
                    <td>{{fMoney(item.payedpenaltymoney)}}</td>
                    <td>{{fMoney(item.resttopaypenaltymoney)}}</td>
                    <td>{{getDictValue(item.status, dict.delayStatus)}}</td>
                </tr>
            </tbody>   
        </table>
              <!--暂无数据 start-->
        <div class="noData">
            {{errorInfo}}
        </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
    /*
     * 贷款管理主页
     */
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import page from '../basic/page/page.vue'


import action from '../../action/interface.js'
import mixin from '../mixin.js' 
import dict from '../../js/dict.js'

var pagesize = 8
export default {
    components: {nameCondition, mobileCondition, formGroup, queryBtnGroup, page, dateCondition}
    ,mixins: [mixin]
    ,data () {
        return {'mobile': '', 'uname': '','loanNo': '','status': '', 'overdueStartTime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'dict': dict,'pid':''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.delayQuery
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'mobile': this.mobile, 'uname': this.uname, 'loanNo': this.loanNo, 'overdueStartTime': this.overdueStartTime, 'status': this.status, 'offset': _offset, 'pagesize': pagesize,'pid':this.pid}
        }
    }
    ,created () {
        this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="overdueStartTime"]').datetimepicker().on('changeDate', function(ev){
            vm.overdueStartTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
    }
    ,methods: {
        clearAccount (detail) {
            eventHub.$emit('open-modal', returnMoney, {'detail': detail})
        }
    }
}
</script>


