<style>
    #authEdit .control-label{
        width: 100px;
        text-align: right;
    }
    #authEdit .valid-error{
        padding-left: 105px;
    }
    #authEdit .txtarea .valid-error{
        bottom:-20px;
    }
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">修改授权有效期</h4>
        </div>
        <div class="modal-body">
            <form id="authEdit">
            <div class="form-group">
                <label class="control-label">授权类型：</label>
                <select v-model="params.id" class="form-control" style="width: 100px;display: inline-block;">
                    <option value="7">京东</option>
                    <option value="6">淘宝</option>
                    <option value="5">支付宝</option>
                    <option value="3">运营商</option>
                    <option value="2">芝麻信用</option>
                </select>
            </div>
            <div class="form-group">
                <label class="control-label">有效期修改为：</label>
                <input type="text" class="form-control inline-input" name="expiredDay" v-model="params.expiredDay" required />
                <span>天</span>
            </div>
            <div class="form-group txtarea">
                <label class="control-label">备注：</label>
                <textarea class="note" v-model="params.remark" required></textarea>
            </div>
            </form>
        </div>
        <div class="modal-footer">
            <save-back-btn></save-back-btn>
        </div>
    </div>
</template>
<script>
/*
 * 修改授权（全部用户）
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import saveBackBtn from '../query-btn-group/save-back-btn.vue'


export default {
    components: {saveBackBtn}

    ,data () {
        return {

            params: {id: 'jd', expiredDay: '', remark: ''}

        }
    }
    ,mounted () {
        eventHub.$on('do-save', this.doSave)
        eventHub.$on('go-back', this.goBack)
        $('#authEdit').validate({
            rules: {
                'expiredDay': {
                    'number': true
                    ,'min': 0
                }
            }
        })
    }
    ,methods: {

        //保存
        doSave () {
            if ($('#authEdit').valid()){
                let _params = this.params
                let saveAction = action.rootPath + action.interface.updateCreditTag
                this.post(saveAction, _params, function (res) {
                    if(res.code == 403){
                        eventHub.$emit('show-alert', res.errorMsg);
                    }else if (res.errorcode == 0) {
                        eventHub.$emit('show-alert', '保存成功！')
                    } else {
                        eventHub.$emit('show-alert', res.message)
                    }
                })                    
            }

        }
        ,goBack () {//返回
            eventHub.$emit('close-modal')
        }
    }
    ,mixins: [mixins]

}
</script>


