<style>
  .back {
    margin-left: 20px;
  }
</style>
<template>
    <div>
        <form-group>
            <div class="form-horizontal  col-md-4" slot="input">
                <label class="control-label col-md-3">日期</label>
                <div class="col-md-7">
                    <date-condition class="form-control" q-name="validdate" v-model="validdate"></date-condition>    
                </div>          
            </div>
            <div class="form-group col-md-3" slot="input">
                <label class="control-label col-md-4 whp-n">产品类型：</label>
                <div class="col-md-8">
                    <select class="form-control" v-model="pid">
                        <option value="">全部</option>
                        <option value="1">随想贷</option>
                        <option value="3">车贷</option>
                        <option value="2">微米贷</option>
                        <option value="4">多米贷</option>
                    </select>
                </div>
            </div>
            <query-btn-group slot="btn">
                <footer-btn-group></footer-btn-group>
            </query-btn-group>
                <a slot="btn" type="button" class="btn btn-default btn-sm btn-outline back" href="javascript:;" @click="back"><i class="icon-reply"></i>&nbsp;返回</a>
            <!-- <button slot="btn" type="button" class="btn btn-default" @click="back">返回</button> -->

        </form-group>
        <div>
            <table class="table table-bordered table-striped">
              <thead>
              <tr>
                <th>序号</th>
                <th>客户姓名</th>
                <th>客户ID</th>
                <th>身份证号码</th>
                <th>是否白名单</th>
                <th>额度类型</th>
                <th>总额度（元）</th>
                <th>额度生效日期</th>
                <th>额度失效日期</th>
                <th>额度有效期（天）</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item, index) in reDataList">
                <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                <td>{{item.uname}}</td>
                <td>{{uid}}</td>
                <td>{{item.idcard}}</td>
                <td><span v-if="item.creditfrom == 1">是</span><span v-else>否</span></td>
                <td>{{item.productname}}</td>
                <td>{{fMoney(item.creditmoney)}}</td>
                <td>{{item.validdate}}</td>
                <td>{{item.invaliddate}}</td>
                <td>{{item.validterm}}</td>
              </tr>
              </tbody>
            </table>
            <!--暂无数据 start-->
            <div class="noData">
              {{errorInfo}}
            </div>
            <!--暂无数据 end-->
        </div>
        <page :pageInfo="pageInfo" :total="total"></page>
    </div>

</template>
<script>
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'
import footerBtnGroup from '../basic/footer-btn/footer-btn-group.vue'

//var url = action.rootPath + action.interface.creditQueryhistory
var pagesize = 8
    export default {

        components: {queryBtnGroup, dateCondition, formGroup, page, footerBtnGroup}
        ,data () {
            return {'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'validdate': '', dict, 'uid': this.$route.query.uid,'pid':''}
        }
        ,computed: {
          //分页用
            'pageInfo': function () {
                return {'pageSize': pagesize, 'currentpage': this.pageNum}
            }
            ,'qParams': function () {
                let _offset = this.cptOffset(this.pageNum, pagesize)
                return {'uid': this.$route.query.uid,'pid':this.pid, 'offset': _offset, 'pagesize': pagesize, 'validdate': this.fDate(this.validdate, 'yyyy-MM-dd')}
            }
            ,'url': function () {
              return action.rootPath + action.interface.creditQueryhistory
            }
        }
        ,created () {
            //初始化时入时，查询数据
            this.doQuery()
        }
        ,methods: {
            back () {
                this.$router.go(-1)
            }
        }
        ,updated () {
            let vm = this
            $('input[name="validdate"]').datetimepicker().on('changeDate', function(ev){
                vm.validdate = ev.date.valueOf()
            });
 
        }
        ,mixins: [mixins]
        ,activated () {
            this.doQuery()
            //进入时，先请空日期，避免上次选择的影响
            $('.date-input').val('')

        }
        ,deactivated () {
            //退出时validdate
            this.validdate = ''
        }
    }
</script>


