<template>
   <div class="infoTable clearfix">
      <table class="table table-bordered table-striped">
        <thead>
        <tr>
          <th>时间</th>
          <th>地址</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in reDataList">
          <td>{{timeNormal(item.createtime)}}</td>
          <td>{{item.address}}</td>
        </tr>
        </tbody>
      </table>
      <div class="noData" style="padding:0">
        {{errorInfo}}
      </div>
      <page :page-info="pageInfo" :total="total"></page>
    </div>
</template>

<script>

import action from '../../action/interface.js'
import mixins from '../mixin.js'
import page from '../basic/page/page.vue'

var pagesize = 10;
export default {
        data () {
            return {'reDataList': [],'errorInfo': '','id':this.$route.query.id,'pageNum': 1, 'total': 0}
        }
        ,components:{page}
        ,computed: {
          //分页用
           'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
           }
           ,'qParams': function () {
             let _offset = this.cptOffset(this.pageNum, pagesize)
             return {'id':this.id,'offset': _offset, 'pageSize': pagesize}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.userSeat
           }
       }
       ,mixins: [mixins]
       ,created () {
          this.doQuery()
          //监听go-page事件
          eventHub.$on('go-page', this.goPage);
       }
       ,destroyed (){
          eventHub.$off('go-page', this.goPage);
       }
  }
</script>


