<template>
  <div class="infoTable clearfix">
            <table class="table table-bordered">
              <thead>
              <tr>
                <th>交易时间</th>
                <th>交易对象</th>
                <th>流水号</th>
                <th>交易金额（元）</th>
                <th>支付方式</th>
                <th>状态</th>
                <th>备注</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for='item in reDataList'>
                <td>{{item.tradetime}}</td>
                <td>{{item.counterparty}}</td>
                <td>{{item.tradenumber}}</td>
                <td>{{item.tradeamount}}</td>
                <td>{{item.phonenumber}}</td>
                <td>{{item.tradestatus}}</td>
                <td>{{item.comments}}</td>
              </tr>
              </tbody>
            </table>
            <div class="noData" style="padding:0">
              {{errorInfo}}
            </div>
            <page :page-info="pageInfo" :total="total"></page>
          </div>

</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import page from '../basic/page/page.vue'
var pagesize = 10;
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': '','pageNum': 1, 'total': 0}
        }
        ,components:{page}
        ,computed: {
          //分页用
           'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
           }
           ,'qParams': function () {
             let _offset = this.cptOffset(this.pageNum, pagesize)
              return {'id':this.id,'offset': _offset, 'pageSize': pagesize}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.zfbRecord
           }
       }
       ,mixins: [mixins]
       ,created () {
          this.doQuery()
          //监听go-page事件
          eventHub.$on('go-page', this.goPage);
       }
       ,destroyed (){
          eventHub.$off('go-page', this.goPage);
       }
  }
</script>



