
<template>
    <div>
    <button type="button" class="btn btn-default btn-outline" @click="goBack">返回</button>
       <div class="infoTab">
    <div class="title col-xs-12">实名认证信息</div>
    <div class="col-xs-12 info clearfix">
      <div class="col-xs-4">
        <p>
          <label>客户姓名：</label>
          <span>{{tableData.uname}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证号：</label>
          <span>{{tableData.idcard}}</span>
        </p>
      </div>
    </div>
  </div>
  <div class="infoTab">
    <div class="title col-xs-12">绑卡信息</div>
    <div class="col-xs-12 info clearfix">
      <div class="col-xs-4">
        <p>
          <label>姓名：</label>
          <span>{{tableData.uname}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证号：</label>
          <span>{{tableData.idcard}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>卡号：</label>
          <span>{{tableData.bankno}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>所属银行：</label>
          <span>{{tableData.openbank}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>银行预留手机号：</label>
          <span>{{tableData.bankmobile}}</span>
        </p>
      </div>
    </div>
  </div>
    </div>
</template>

<script>
import action from '../../action/interface.js'
import mixin from '../mixin.js'

    export default {
        data () {
            //console.log(111)
            return {'id': '','tableData': [], 'reResult': ''}
        }
        ,created: function() {
        //eventHub.$on('add-tab')
        var id = this.$route.query.id;
        console.log(id);
        //console.log(id);
        //aParams.id = id;
        //console.log(aParams.id);
        }
      ,methods: {
         goBack () { //返回
            this.$router.go(-1)
        }
        ,renderTable () {
            let vm = this;
            let aUrl = action.rootPath + action.interface.queryone;
            let aParams = {'id': ''};
            aParams.id = vm.$route.query.id;
            vm.send(aUrl, aParams, function (res) {
                if ( res.errorcode == 0 ) {
                    vm.tableData = res.result;
                    //console.log(vm.tableData);
                    if (vm.total <= 0) vm.reResult = "暂无数据";
                } else {
                    vm.tableData = [];
                    vm.reResult = res.message + "(errorcode: )" + res.errorcode;
                } 
            })
        }
        }
        ,activated () {
           this.renderTable ();
        }
        ,mixins: [mixin]
    }
</script>


