<style lang="scss">
    @import './alert-whitelist.scss';
</style>
<style>
    .change-edit-body {
        padding-top: 30px;
        padding-bottom: 30px;
    }
</style>
<template>
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">批量导入</h4>
          </div>

          <div class="modal-body">
            <div class="col-xs-12 form-inline group-left validateModal center-block  uploading">
              <div class="form-group col-xs-12">
              <div class="form-group">
              <form action="" method="post" id="upload-form" enctype='multipart/form-data'>
                <input type="file" id="excelList" name="excelList" accept=".xls,.xlsx"/>
                  <p class="help-block">提示：支持excel上传</p>  
              </form>    
             </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" @click="upLoad">上传</button>
            </div>
          </div>
          <!-- /.modal-content -->
        </div>
</template>
<script>
/*
 * 导入excel名单
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'


var url = action.rootPath + action.interface.whiteUpload
    export default {
       data () {
            return {'excelList': ''}
        }
        ,methods: {
        upLoad () {
        var files = $("#excelList")[0].files[0], fileName = '';
        //添加上传信息
        var filesLen = files.length;
        var html = "";
        $('#upload-list').html(html);
        var data = {                //传递参数
          fileName : fileName
        };
        
        $('#upload-form').ajaxSubmit({
          url: url,
          type: 'post',
          data: data,
          success: function(res){
            res = JSON.parse(res);
            if(res.code == 403){
                eventHub.$emit('show-alert', res.errorMsg);
            }else if (res.errorcode == 0) {
                eventHub.$emit('show-alert', '文件上传成功！')
            } else {
                eventHub.$emit('show-alert', '文件上传失败！')
            }
             }
          })
      }
    }
      ,mixins: [mixins]
    }
</script>


