<template>
  <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>总资产：</label>
                  <span>{{reDataList.totalmoney}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>小金库余额：</label>
                  <span>{{reDataList.balance}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>理财金额：</label>
                  <span>{{reDataList.fund}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>钱包可用余额：</label>
                  <span>{{reDataList.walletmoneyavailable}}元</span>
                </p>
              </div>
            </div>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.jdWealth
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



