<template>
  <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>姓名：</label>
                  <span>{{reDataList.realname}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>性别：</label>
                  <span v-if='reDataList.gender==0'>男</span>
                  <span v-if='reDataList.gender==1'>女</span>
                  <span v-else>未确认</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>昵称：</label>
                  <span>{{reDataList.nick}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>身份证：</label>
                  <span></span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>手机号：</label>
                  <span>{{reDataList.phonenumber}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>邮箱：</label>
                  <span>{{reDataList.email}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>注册时间：</label>
                  <span>{{fDate(reDataList.registerdate,'yyyy-MM-dd')}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>绑定微博：</label>
                  <span>{{reDataList.weibonick}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>成长值：</label>
                  <span>{{reDataList.vipcount}}</span>
                </p>
              </div>

            </div>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.jdBase
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



