<style lang="scss">
    @import '../basic/alert/alert-msg.scss';
</style>
<style>
    .change-edit-body {
        padding-top: 30px;
        padding-bottom: 30px;
    }
    
    #btn-cancel {
        color: #333;
        background-color: #fff;
        border-color: #ccc;
    }
</style>
<template>
    <div class="modal-content alert-content">
        <div class="alert-head">修改额度有效期</div>
        <div class="change-edit-body">
            <form class="form-horizontal" id="changeTime">
                <div class="form-group col-md-12">
                    <label class="col-md-7 control-label">额度有效期修改为：</label>
                    <div class="col-md-3">
                        <input type="text" class="form-control" name="validterm" v-model="validterm" required/>
                    </div>
                    <div class="col-md-1" style="padding-top: 7px;">天</div>
                </div>
            </form>
        </div>
        <div class="alert-footer">
            <button type="button" @click="postChange">确认</button>
            <button type="button" id="btn-cancel" data-dismiss="modal">取消</button>
        </div>
    </div>
</template>
<script>
/*
 * 修改额度有效期
 * params : {modalProps：{uids}}
 */
    import mixins from '../mixin.js'
    import action from '../../action/interface.js'
    

    export default {
        props: ['modalProps']
        ,data () {
            return {'validterm': ''}
        }
        ,methods: {
            //提交修改
            postChange () {
                let vm = this
                var url = action.rootPath + action.interface.creditBatchupdate
                var onceUrl = action.rootPath + action.interface.creditUpdate
                var params = {'validterm': '', 'uids': ''}
                if($('#changeTime').valid()){
                    let uids = vm.modalProps.uids
                        this.send(url, {'validterm': vm.validterm, 'uids': vm.modalProps.uids}, function (res) {
                            if(res.code == 403){
                                eventHub.$emit('show-alert', res.errorMsg);
                            }else if (res.errorcode == 0 ) {
                                eventHub.$emit('show-alert', '保存成功')                       
                            } else {
                                eventHub.$emit('show-alert', res.message)
                            }
                    })    

                                    
                }


            }
        }
        ,mixins: [mixins]
    }
</script>


