<template>
    <div class="pd15">
       <div class="infoTab">
    <div class="title col-xs-12">基本信息</div>
    <div class="col-xs-12 info clearfix" v-if="reDataList.baseinfo">
      <div class="col-xs-4">
        <p>
          <label>客户姓名：</label>
          <span>{{reDataList.baseinfo.customername}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证号：</label>
          <span>{{reDataList.baseinfo.idcard}}</span>
        </p>
      </div>
       <div class="col-xs-4">
        <p>
          <label>出生日期</label>
          <span>{{reDataList.baseinfo.birth}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>证件地址：</label>
          <span>{{reDataList.baseinfo.idaddress}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>最高学历：</label>
          <span>{{getDictValue(reDataList.baseinfo.education,dict.education)}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>额度申请照片比对结果：</label>
          <span><a href="javascript:;" class="blue" @click="showcompareimgs(1)">查看</a></span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>借款照片比对结果：</label>
          <span><a href="javascript:;" class="blue" @click="showcompareimgs(2)">查看</a></span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>OCR识别结果：</label>
          <span><a href="javascript:;" class="blue" @click="showOcrEdit(reDataList.baseinfo.edited,reDataList.baseinfo.photocopy,reDataList.baseinfo.screen,reDataList.baseinfo.tempidphoto,reDataList.baseinfo.idphoto)">查看</a></span>
        </p>
      </div>
      <div class="col-xs-4">
        <p style="white-space: nowrap">
          <label>OCR识别结果是否经过人为修改：</label>
          <span v-if="reDataList.baseinfo.ocredited==true">是</span>
          <span v-else>否</span>
          <a v-if="reDataList.baseinfo.ocredited==true" href="javascript:;" class="blue" @click="showOcrEditDetail(reDataList.baseinfo.customername,reDataList.baseinfo.idcard,reDataList.baseinfo.editedname,reDataList.baseinfo.editedidcardnum)">查看修改详情</a>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证正面：</label>
          <span><a :href="reDataList.baseinfo.frontimgurl"><img :src="reDataList.baseinfo.frontimgurl" /></a></span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证反面：</label>
          <span><a :href="reDataList.baseinfo.backimgurl"><img :src="reDataList.baseinfo.backimgurl" /></a></span>
        </p>
      </div>
    </div>
    <div class="noData">
       {{errorInfo}}
    </div>
  </div>
  <div class="infoTab">
    <div class="title col-xs-12">居住地和联系信息</div>
    <div class="col-xs-12 info clearfix" v-if="reDataList.connectioninfo">
      <div class="col-xs-4">
        <p>
          <label>居住地址：</label>
          <span>{{reDataList.connectioninfo.address}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>手机号码：</label>
          <span>{{reDataList.connectioninfo.mobile}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label></label>
          <span></span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector1">
        <p>
          <label>与紧急联系人关系：</label>
          <span>{{reDataList.connectioninfo.connector1.nexus}}</span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector1">
        <p>
          <label>紧急联系人：</label>
          <span>{{reDataList.connectioninfo.connector1.name}}</span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector1">
        <p>
          <label>紧急联系人手机号码：</label>
          <span>{{reDataList.connectioninfo.connector1.phone}}</span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector2">
        <p>
          <label>与紧急联系人关系：</label>
          <span>{{reDataList.connectioninfo.connector2.nexus}}</span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector2">
        <p>
          <label>紧急联系人：</label>
          <span>{{reDataList.connectioninfo.connector2.name}}</span>
        </p>
      </div>
      <div class="col-xs-4" v-if="reDataList.connectioninfo.connector2">
        <p>
          <label>紧急联系人手机号码：</label>
          <span>{{reDataList.connectioninfo.connector2.phone}}</span>
        </p>
      </div>
    </div>
    <div class="noData">
       {{errorInfo}}
    </div>
  </div>
  <div class="infoTab">
    <div class="title col-xs-12">银行卡信息</div>
    <div class="col-xs-12 info clearfix"  v-if="reDataList.bankinfo">
      <div class="col-xs-4">
        <p>
          <label>开户银行：</label>
          <span>{{reDataList.bankinfo.openbank}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>银行卡号：</label>
          <span>{{reDataList.bankinfo.bankno}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>银行手机预留号：</label>
          <span>{{reDataList.bankinfo.bankmobile}}</span>
        </p>
      </div>

    </div>
    <div class="noData">
       {{errorInfo}}
    </div>
  </div>

    </div>
</template>

<script>
import ocrInfo from './ocr-info.vue'
import ocrInfoDetail from './ocr-info-detail.vue'
import compareImgs from './compare-imgs.vue'
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

let queryUserPhotoUrl = action.rootPath + action.interface.queryUserPhoto
export default {
        data () {
            return {'id': this.$route.query.id,'reDataList': [],'errorInfo': '','dict': dict,'ocrInfoList':[]}
        }
        ,components:{ocrInfo,ocrInfoDetail,compareImgs}
        ,computed: {
           'qParams': function () {
             return {'id': this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.basicInfo
           }
       }
       ,methods: {
        //查看OCR比对结果
        showOcrEdit (edited,photocopy,screen,tempidphoto,idphoto) {
            eventHub.$emit('open-modal', ocrInfo, {'modalW':'350px','edited':edited,'photocopy':photocopy,'screen':screen,'tempidphoto':tempidphoto,'idphoto':idphoto});
        }
        //查看修改详情
        ,showOcrEditDetail (customername,idcard,editedname,editedidcardnum) {
            eventHub.$emit('open-modal', ocrInfoDetail, {'modalW':'500px','customername':customername,'idcard':idcard,'editedname':editedname,'editedidcardnum':editedidcardnum});
        }
        //查看照片对比结果
        // ,showcompareimgs (imgrefurl,confidence,comparedImgUrl) {
        //   if(comparedImgUrl&&imgrefurl&&confidence>=0){
        //     eventHub.$emit('open-modal', compareImgs, {'modalW':'800px','imgrefurl':imgrefurl,'confidence':confidence,'comparedImgUrl':comparedImgUrl});
        //   }else{
        //     eventHub.$emit('show-alert', '暂无数据！')
        //   }

        // }
        ,showcompareimgs (businessType) {
            let vm = this
            let _params = {businessType:businessType,uid:this.$route.query.id}
            vm.post(queryUserPhotoUrl, _params, function(res) {
                if (res.errorcode == 0 && res.result) {
                  if(res.result.comparedimgs&&res.result.imgrefurl&&res.result.confidence>=0){
                    eventHub.$emit('open-modal', compareImgs, {'modalW':'800px','imgRefUrl':res.result.imgrefurl,'confidence':res.result.confidence,'comparedImgUrl':res.result.comparedimgs});
                  }else{
                    eventHub.$emit('show-alert', '暂无数据！')
                  }
                } else{
                    eventHub.$emit('show-alert', '暂无数据！')
                  }
            })
        }
       }
       ,mixins: [mixins]
       ,created () {
          this.id = this.$route.query.id;
           this.doQuery()
       }
       ,activated () {
         if(this.id != this.$route.query.id){
           this.id = this.$route.query.id;
           this.doQuery()
         }
       }
  }
</script>



