
<style lang="scss">
    @import "./nav-tree.scss";

</style>
    <template>
    <transition name="slide-left">
        <div class="nav-tree" v-show="show">
            <div class="nav-header">
                <img/>
                <div>
                    <div class="text-weight">欢迎您！</div>
                    <div class="text-weight">{{uname}}</div>
                </div>            
                <a href="javascript:;" class="close" @click="toggleNav">×</a>
            </div>

            <div class="tree">
                <ul class="root-menu">
                    <template v-for="(list, index) in menuList">
                        <li :class="{active: list.checked}">
                            <i :class="[list.checked ? 'icon-caret-down' : 'icon-caret-right', 'sub-control']" v-if="list.children"></i>
                            <a href="javascript:void(0);" v-if="list.type == 0" @click.stop="clickNode(list.id)">
                                <i class="icon-list"></i>
                                <span>{{list.name}}</span>
                            </a>
                            <a href="javascript:void(0);" @click="clickNode(list.id)" v-else>
                                <i class="icon-list"></i>{{list.name}}
                            </a>
                            <ul class="sub-menu" v-show="list.type == 0 && list.checked" :class="{'active': list.checked, 'checked': list.checked}">
                                <template v-for="item in list.children">
                                    <li  :class="{'blue-header': item.checked}">
                                        <a href="javascript:void(0);" @click="clickNode(item.id)">{{item.name}}</a>
                                    </li>                                    
                                </template>

                                
                            </ul>
                        </li>                        
                    </template>

                </ul>
            </div>

        </div>
    </transition>
    </template>
<script>
/*
 * 导航菜单
 * api: {'toggle-nav': 开合导航}
 */
import mixins from '../../mixin.js'
import action from '../../../action/interface.js'
//测试模拟数据
    let data = {"column":{"id":45,"name":"菜单","description":"","data":"","type":0,"children":[{"id":46,"name":"客户管理","description":"客户管理","data":"","type":0,"children":[{"id":71,"name":"白名单管理","description":"白名单管理","data":"/white/query","type":1},{"id":56,"name":"客户管理","description":"客户管理","data":"/customerManagement","type":1}]},{"id":52,"name":"授权管理","description":"授权管理","data":"/authManage","type":1},{"id":48,"name":"授信管理","description":"授信管理","data":"/creditManage","type":1},{"id":127,"name":"贷款管理","description":"贷款管理","data":"/loanManage","type":1},{"id":147,"name":"贷后管理","description":"贷后管理","data":"/postLoanManage","type":1},{"id":146,"name":"逾期管理","description":"逾期管理","data":"/delayManage","type":1},{"id":51,"name":"合同管理","description":"合同管理","data":"/contractManage","type":1},{"id":50,"name":"产品管理","description":"产品管理","data":"/prodManage","type":1},{"id":47,"name":"贷款统计","description":"贷款统计","data":"","type":0,"children":[{"id":73,"name":"贷款统计信息","description":"贷款统计信息","data":"/loanStatistics","type":1},{"id":75,"name":"逾期统计","description":"逾期统计","data":"/delayStatistics","type":1}]},{"id":49,"name":"登录日志","description":"登录日志","data":"","type":0,"children":[{"id":74,"name":"登录日志列表","description":"登录日志列表","data":"loginQuery","type":1},{"id":76,"name":"登录日志汇总信息","description":"登录日志汇总信息","data":"/loginStatistics","type":1}]},{"id":148,"name":"操作日志","description":"操作日志","data":"/logOperation","type":1}]},"userName":"pan.junlin"}

    let menuList = data.column.children

    //console.log(menuList)
    export default {
        mixins: [mixins]
        ,data () {
            return {
                'menuList': [] //导航菜单数据
                ,'openMenu': ''  //打开的是哪个二级菜单，menuList中的ID对应
                ,'uname': '' //用户姓名
                ,'show': true
            }
        }
        ,created () {
            //创建关闭导航事件
            eventHub.$on('toggle-nav', this.toggleNav)
            // 请求导航数据
            let menuAction = action.rootPath + action.interface.menu
            let vm = this

            //测试环境需要
            // let _list = menuList
            // _list.map(function (item) {
            //     //父节点赋值
            //     item.checked = false
            //     //子节点赋值
            //     if (item.type == 0) {
            //         let _children = item.children
            //         _children.map(function (child) {
            //             child.checked = false                      
            //         })
            //     }
            // })
            // vm.menuList = _list  
            //测试环境需要

           vm.post(menuAction, {}, function (res) {
                //请求返回的数据多一级，过滤
                //res = JSON.parse(res).result;
                //res = res.result;
                console.log(res)
                //console.log(typeof(res))
                if(res.column.children){
                    let _list = res.column.children
                    let _id = 1 ,_childId = 20;

                    _list.map(function (item) {
                        //父节点赋值
                        _id = _id +1;
                        item.id = _id;
                        item.checked = false
                        //子节点赋值
                        if (item.type == 0) {
                            let _children = item.children
                            _children.map(function (child) {
                                _childId = _childId + 1;
                                child.id = _childId;   
                                child.checked = false                      
                            })
                        }
                    })
                    vm.menuList = _list   
                    vm.uname = res.userName
                    //触发显示姓名事件，把username上传到父节点
                    eventHub.$emit('show-uname', res.userName)         
                }
            })
        }
        ,methods: {
            //关闭导航
            toggleNav () {
                this.show = !this.show
            }
            //点击节点 
            ,clickNode (_nodeId) {
                let _list = this.menuList
                let _curItem;
                let menuStatus;
                //遍历根节点
                _list.map(function(item){
                    //如果是当前节点
                    if (_nodeId == item.id) {
                        _curItem = item
                        //当前节点有子节点，则checked取非
                        if (item.type == 0) {
                            item.checked = !item.checked
                        } else {        //当前节点无子节点，则checked = true
                            item.checked = true
                        }
                    } else { //如果父节点不匹配,且点击是非根节点时，继续遍历子节点
                        if (item.type == 0) {
                            let _children = item.children
                            let _clickChild = false //子节点是否命中
                           //遍历子节点
                            _children.map( function(child) {
                               if (_nodeId == child.id) {
                                    _curItem = child
                                    child.checked = true
                                    _clickChild = true
                                } else {        
                                    child.checked = false
                                }
                           }) 
                            //子节点未命中，则关闭子节点
                            if (!_clickChild) {
                                item.checked = false
                            }
                        } else {
                            item.checked = false
                        }
                    }
                })
                //this.$set('menuList', _list)
                this.menuList = _list
                //切换 tab
                console.log(JSON.stringify(_curItem))
                if (_curItem.type == 1) {
                // tab-nav
                    eventHub.$emit('add-tab', _curItem.data, _curItem.name)
                //change route
                    this.$router.push(_curItem.data)
                }
            }
        }
    }
</script>



