<template>
    <div>
        <form-group>
            <temp-condition slot="input" s-name="合同名称：">
                <input type="text" class="form-control" v-model="name" />
            </temp-condition>
            <temp-condition slot="input" s-name="合同编号：">
                <input type="text" class="form-control" v-model="sno" />
            </temp-condition>
            <temp-condition slot="input" s-name="借款人：">
                <input type="text" class="form-control" v-model="loanUser" />
            </temp-condition>
            <div class="form-group col-md-6" slot="input">
                <label class="control-label col-md-3">
              签订日期：
            </label>
                <date-condition q-name="signStartTime" q-class="form-control" v-model="signStartTime"></date-condition>
                <span slot="input">-</span>
                <date-condition q-name="signEndTime" q-class="form-control" v-model="signEndTime"></date-condition>

            </div>
            <div class="form-group col-md-3" slot="input">
                <label class="control-label col-md-4 whp-n">产品类型：</label>
                <div class="col-md-8">
                    <select class="form-control" v-model="pid">
                        <option value="">全部</option>
                        <option value="1">随想贷</option>
                        <option value="3">车贷</option>
                        <option value="2">微米贷</option>
                        <option value="4">多米贷</option>
                    </select>
                </div>
            </div>
            <query-btn-group slot="btn"></query-btn-group>
        </form-group>
        <div class="table-responsive">
            <table class="table table-bordered table-striped">
                <thead>
                    <tr>
                        <th>合同名称</th>
                        <th>合同编号</th>
                        <th>合同模板编号</th>
                        <th>产品类型</th>
                        <th>借款人</th>
                        <th>出借人</th>
                        <th>签订日期</th>
                        <th>操作</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(row, index) in reDataList">
                        <td>{{row.name}}</td>
                        <td>{{row.sno}}</td>
                        <td><a href="javascript:;">{{row.tempno}}</a></td>
                        <td>{{row.productname}}</td>
                        <td>{{row.loanuser}}</td>
                        <td><a href="javascript:;" @click="showInvestUsers(row.investuser)">查看</a></td>
                        <td>{{fDate(row.signtime, 'yyyy-MM-dd')}}</td>
                        <td v-if="row.signedurl"><a :href="row.signedurl" target="_blank">查看</a></td>
                        <td v-else><a href="javascript:;">查看</a></td>
                    </tr>
                </tbody>
            </table>
            <!--暂无数据 start-->
            <div class="noData">
                {{errorInfo}}
            </div>
            <!--暂无数据 end-->
        </div>
        <page :page-info="pageInfo" :total="total"></page>
    </div>
</template>
<script>
/*
 * 合同管理
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import tempCondition from '../form-group/temp-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'
import contractMould from './contract-mould.vue'
import investUsers from './invest-users.vue'


let pagesize = 8
export default {
    data () {
        return {'name': '', 'sno': '', 'loanUser': '', 'signStartTime': '', 'signEndTime':'', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '','pid':''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.queryContract
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'name': this.name, 'sno': this.sno, 'loanUser': this.loanUser, 'signStartTime': this.signStartTime, 'signEndTime': this.signEndTime, 'offset': _offset, 'pagesize': pagesize,'pid':this.pid}
        }

    }
    ,components: {queryBtnGroup, dateCondition, tempCondition, formGroup, page, contractMould,investUsers}
    ,created () {
        this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="signStartTime"]').datetimepicker().on('changeDate', function(ev){
            vm.signStartTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
        $('input[name="signEndTime"]').datetimepicker().on('changeDate', function(ev){
            vm.signEndTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });  
    }
    ,methods: {
        showContract (id) {
             eventHub.$emit('open-modal', contractMould,{modalW:"840px",'id':id});
        } 
        ,showInvestUsers (id) {
            eventHub.$emit('open-modal', investUsers,{'id':id});
        }
    }
    ,mixins: [mixins]
}
</script>


