<style lang="scss">
    @import './show-bid.scss';
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">续贷发标</h4>
        </div>
        <div class="modal-body">
            <form action="#" method="post" id="deleteWhite" class="regForm">
                <div class="col-xs-12 form-inline group-left validateModal loan-alert">
                    <div class="form-group col-xs-12 clearfix">
                        <p>
                            <label for="" class="fl">客户姓名：</label>{{modalProps.addbidList.uname}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <p>
                            <label for="" class="fl"> 客户ID：</label>{{modalProps.addbidList.uid}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <p>
                            <label for="" class="fl">贷款编号：</label>{{modalProps.addbidList.loanNo}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <p>
                            <label for="" class="fl">当前状态：</label>{{getDictValue(modalProps.addbidList.status, dict.loanStatus)}}</p>
                    </div>
                    <div><p class="tc">提示：操作之前请确认该笔贷款是否已通过续贷审核且未完成发标</p></div>
                </div>
            </form>
            </div>
            <div class="modal-footer">
                 <span>
                    <button type="button" class="btn btn-primary" @click="doSave">确认发标</button>
                    <button type="button" class="btn btn-default" @click="goBack">取消</button>
                </span>
            </div>
        </div>
        <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 补标
 * params: {modalProps: {id: 记录id}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import saveBackBtn from '../query-btn-group/save-back-btn.vue'

var saveAddBid = action.rootPath + action.interface.addBid
    export default {
        props: ['modalProps']
        ,data () {
            return {'uname': '', 'idcard': '', 'mobile': '', 'remark': '','dict': dict}
        }
        ,components: {saveBackBtn}

        ,created () {
        }
        ,mounted () {
            console.log(this.modalProps.addbidList)
            eventHub.$on('go-back', this.goBack)
        }        
        ,methods: {
            //保存
            doSave () {
                // let _params = {'originBid':this.modalProps.addbidList.originBid,'status':this.modalProps.addbidList.status}
                this.post(saveAddBid, this.modalProps.addbidList, function (res) {
                    if(res.code == 403){
                            eventHub.$emit('show-alert', res.errorMsg);
                        }else if (res.errorcode == 0) {
                        eventHub.$emit('show-alert', '发标成功！');
                    } else {
                        eventHub.$emit('show-alert', res.message)
                    }
                })    
            }
            ,close () {
                eventHub.$emit('close-modal')
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


