<template>
<div>
<div class="infoTab">
     <div class="title col-xs-12">账户余额</div>
     <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>账户余额：</label>
                  <span>{{reDataList.yue}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>余额宝余额：</label>
                  <span>{{reDataList.yeb}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>余额宝累计收益：</label>
                  <span></span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>招财宝：</label>
                  <span>{{reDataList.zcb}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>基金：</label>
                  <span>{{reDataList.fund}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>存金宝：</label>
                  <span>{{reDataList.cjb}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>淘宝理财：</label>
                  <span>{{reDataList.taolicai}}元</span>
                </p>
              </div>

            </div>
          </div>
</div>
<div class="infoTab">
     <div class="title col-xs-12">蚂蚁花呗</div>
     <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>消费额度：</label>
                  <span></span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>可用额度：</label>
                  <span>{{reDataList.huabailimit/100}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>下期应还：</label>
                  <span></span>
                </p>
              </div>
            </div>
          </div>
</div>
</div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.zfbWealth
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



