<template>
<div>
    <form-group>
        <div class="form-group col-md-4" slot="input">
            <label class="control-label col-md-4">请求id</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="requestId"/>
            </div>
        </div>
        <div class="form-group col-md-4" slot="input">
            <label class="control-label col-md-4">操作url</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="operateUrl"/>
            </div>
        </div>
        <div class="form-group col-md-4" slot="input">
            <label class="control-label col-md-4">操作人账号</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="account"/>
            </div>
        </div>
        <div class="form-group col-md-4" slot="input">
            <label class="control-label col-md-4">操作人姓名</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="name"/>
            </div>
        </div>
        <div class="form-group col-md-4" slot="input">
            <label class="control-label col-md-4">操作时间</label>
            <div class="col-md-8">
                <date-condition q-name="operateTime" q-class="form-control" v-model="operateTime"></date-condition>
            </div>
        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <td></td>
                    <td>操作时间</td>
                    <td>操作人</td>
                    <td>操作账号</td>
                    <td>操作url</td>
                    <td>操作参数</td>
                    <td>操作ip</td>
                    <td>请求id</td>
                    <td>备注</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{fDate(item.operattime,'yyyy-MM-dd')}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.account}}</td>
                    <td>{{item.operaturl}}</td>
                    <td>{{item.operatparam}}</td>
                    <td>{{item.ip}}</td>
                    <td>{{item.requestid}}</td>
                    <td>{{item.remark}}</td>
                </tr>
            </tbody>   
        </table>
              <!--暂无数据 start-->
        <div class="noData">
            {{errorInfo}}
        </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
    /*
     * 贷款管理主页
     */
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import page from '../basic/page/page.vue'


import action from '../../action/interface.js'
import mixin from '../mixin.js' 
import dict from '../../js/dict.js'

var pagesize = 8
export default {
    components: {nameCondition, mobileCondition, formGroup, queryBtnGroup, page, dateCondition}
    ,mixins: [mixin]
    ,data () {
        return {'requestId': '', 'operateUrl': '','account': '','name': '', 'operateTime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'dict': dict}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.logOperation 
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'requestId': this.requestId, 'operateUrl': this.operateUrl, 'account': this.account, 'operateTime': this.operateTime, 'name': this.name, 'offset': _offset, 'pagesize': pagesize}
        }
    }
    ,created () {
        this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="operateTime"]').datetimepicker().on('changeDate', function(ev){
            vm.operateTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')
        });
 
    }
    ,methods: {
    }
}
</script>


