<style lang="scss">
    @import './c-mould.scss'
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">出借人</h4>
        </div>
        <div class="modal-body">
            <div>
                <table class="table table-bordered table-striped mt10">
                    <thead>
                        <tr>
                            <th>出借人姓名</th>
                            <th>出借人ID</th>
                            <th>出借金额（元）</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in reDataList">
                            <td>{{item.username}}</td>
                            <td>{{item.uid}}</td>
                            <td>{{fMoney(item.amount)}}</td>
                        </tr>
                    </tbody>
                </table>
                <!--暂无数据 start-->
                <div class="noData" style="display:block;text-align:center">
                    {{errorInfo}}
                </div>
                <!--暂无数据 end-->
            </div>
        </div>
    </div>
    <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 合同模板
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


var investUsers = action.rootPath + action.interface.investUsers
    export default {
        props: ['modalProps']
        ,data () {
            return {'reDataList':[],'dict':dict}
        }
        ,components: {}

        ,created () {
            //请求详情内容
            let vm = this
            vm.send(investUsers, {'bid': vm.modalProps.id}, function (res) {
                if (res.errorcode == 0) {
                    vm.reDataList = res.result
                    if(res.result.length <= 0){
                        vm.errorInfo = "暂无数据"
                    }
                } else { //返回异常信息
                        vm.errorInfo = res.message
                }
            })
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
        }        
        ,methods: {
            close () {
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


