<template>
<div>
    <form-group>
        <name-condition slot="input" v-model="uname"></name-condition>
        <mobile-condition slot="input" v-model="mobile"></mobile-condition>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">贷款编号</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="loanNo"/>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">放款时间</label>
            <div class="col-md-8">
                <date-condition q-name="loanTime" q-class="form-control" v-model="loanTime"></date-condition>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">状态</label>
            <div class="col-md-8">
                <select class="form-control" v-model="status">
                    <option value="">全部</option>
                    <option value="0">审核中</option>
                    <option value="1">审核未通过</option>
                    <option value="2">放款中</option>
                    <option value="3">还款中</option>
                    <option value="4">逾期</option>
                    <option value="5">已结清</option>
                    <option value="6">放款失败</option>
                    <option value="7">续贷处理中</option>
                    <option value="8">已续贷</option>
                </select>
            </div>

        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4 whp-n">产品类型：</label>
            <div class="col-md-8">
                <select class="form-control" v-model="pid">
                    <option value="">全部</option>
                    <option value="1">随想贷</option>
                    <option value="3">车贷</option>
                    <option value="2">微米贷</option>
                    <option value="4">多米贷</option>
                </select>
            </div>
        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <td></td>
                    <td>客户姓名</td>
                    <td>身份证号</td>
                    <td>手机号码</td>
                    <td>产品类型</td>
                    <td>贷款编号</td>
                    <td>贷款账号</td>
                    <td>贷款申请日期</td>
                    <td>贷款发放日期</td>
                    <td>状态</td>
                    <td>申请金额（元）</td>
                    <td>管理费（元）</td>
                    <td>到账金额（元）</td>
                    <td>还款方式</td>
                    <td>日利率（%）</td>
                    <td>罚息利率（%）</td>
                    <td>管理费率（%）</td>
                    <td>贷款期限（天）</td>
                    <td>操作</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.idcard}}</td>
                    <td>{{item.mobile}}</td>
                    <td>{{item.productname}}</td>
                    <td>{{item.loanno}}</td>
                    <td>{{item.bankcard}}</td>
                    <td>{{fDate(item.loanapplytime, 'yyyy-MM-dd')}}</td>
                    <td>{{fDate(item.loantime, 'yyyy-MM-dd')}}</td>
                    <td>{{getDictValue(item.status, dict.loanStatus)}}</td>
                    <td>{{fMoney(item.loanmoney)}}</td>
                    <td>{{fMoney(item.mngmoney)}}</td>
                    <td>{{fMoney(item.accountmoney)}}</td>
                    <td>{{getDictValue(item.paymentmethod, dict.paymentmethod)}}</td>
                    <td>{{toPercent(item.dailyrate)}}%</td>
                    <td>{{toPercent(item.penaltyinterest)}}%</td>
                    <td>{{toPercent(item.mnginterest)}}%</td>
                    <td>{{item.loanday}}</td>
                    <td>
                        <div v-if="item.status == 2">
                            <a href="javascript:;" @click="issueAgain(item.uname,item.uid,item.loanno,item.status,item.mobile,item.loanmoney,item.dailyrate,item.loanday,item.termtype,item.mngmoney,item.bid)">补标</a> | <a href="javascript:;" @click="revokeBid(item.uname,item.uid,item.loanno,item.status,item.loanmoney,item.pid)">确认撤标</a>
                        </div>
                        <div v-if="item.originloanno && item.status == 0">
                            <a href="javascript:;" @click="addBid(item.uname,item.uid,item.loanno,item.status,item.mobile,item.loanmoney,item.dailyrate,item.loanday,item.termtype,item.mngmoney,item.pid,item.originloanno)">续贷发标</a>
                        </div>
                    </td>
                </tr>
            </tbody>   
        </table>
              <!--暂无数据 start-->
        <div class="noData">
            {{errorInfo}}
        </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
    /*
     * 贷款管理主页
     */
// 结清贷款操作
// <td><a v-if="item.status == 3 || item.status == 4" href="javascript:;" @click="clearAccount(item)">结清贷款</a></td>
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import page from '../basic/page/page.vue'
import showAddBid from './show-add-bid.vue'
import showRevokeBid from './show-revoke-bid.vue'
import showIssueAgain from './show-issueagain.vue'

import returnMoney from './return-money.vue'

import action from '../../action/interface.js'
import mixin from '../mixin.js' 
import dict from '../../js/dict.js'

var pagesize = 8
export default {
    components: {nameCondition, mobileCondition, formGroup, queryBtnGroup, page, returnMoney, dateCondition,showAddBid,showRevokeBid,showIssueAgain}
    ,mixins: [mixin]
    ,data () {
        return {'mobile': '', 'uname': '','loanNo': '','status': '', 'loanTime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'dict': dict,'pid':''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.loanQuery
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'mobile': this.mobile, 'uname': this.uname, 'loanNo': this.loanNo, 'loanTime': this.loanTime, 'status': this.status, 'offset': _offset, 'pagesize': pagesize,'pid':this.pid}
        }
    }
    ,created () {
        this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="loanTime"]').datetimepicker().on('changeDate', function(ev){
            vm.loanTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
    }
    ,methods: {
        clearAccount (detail) {
            eventHub.$emit('open-modal', returnMoney, {'detail': detail})
        }
        //补标
        ,issueAgain (name,uid,loanno,status,mobile,loanMoney,dailyRate,loanDay,termType,mngmoney,id) {
            let addbidList = {'uname': name,'uid': uid,'loanNo': loanno,'status': status,'mobile':mobile,'loanMoney':loanMoney,'dailyRate':dailyRate,'loanDay':loanDay,'termType':termType,'mngmoney':mngmoney,'originBid':id}
            eventHub.$emit('open-modal', showIssueAgain, {'modalW':'500px','addbidList':addbidList});
        }
        //撤标
        ,revokeBid (name,uid,loanno,status,loanMoney,pid) {
            let revokeBidList = {'uname': name,'uid': uid,'loanNo': loanno,'status': status,'loanMoney':loanMoney,'pid':pid}
            eventHub.$emit('open-modal', showRevokeBid, {'modalW':'500px','revokeBidList':revokeBidList});
        }
        //续贷发标
        ,addBid (name,uid,loanno,status,mobile,loanMoney,dailyRate,loanDay,termType,mngmoney,pid,originLoanNo) {
            let addbidList = {'uname': name,'uid': uid,'loanNo': loanno,'status': status,'mobile':mobile,'loanMoney':loanMoney,'dailyRate':dailyRate,'loanDay':loanDay,'termType':termType,'mngMoney':mngmoney,'pid':pid,'originLoanNo':originLoanNo}
            eventHub.$emit('open-modal', showAddBid, {'modalW':'500px','addbidList':addbidList});
        }
    }
}
</script>


