<style lang="scss">
    @import './order-detail.scss';
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">照片对比结果</h4>
        </div>
        <div class="modal-body compare-imgs">
            <div class="clearfix">
                <p><label for="" class="fl">对比得分：</label>{{modalProps.confidence}}%</p>
            </div>
            <div class="clearfix">
                <div class="col-xs-6">
                    <p class="tc">待核实照片</p>
                    <img :src="modalProps.imgRefUrl"/>
                </div>
                <div class="col-xs-6 compare-imgs-right">
                    <p class="tc">对比照片（{{nowNum}}/{{modalProps.comparedImgUrl.length}}）</p>
                    <i class="icon-chevron-left blue" @click="goLeft"></i>
                    <i class="icon-chevron-right blue" @click="goRight"></i>
                    <img :src="compareImg"/>
                </div>
            </div>
        </div>
        <div class="modal-footer" style="margin-top:0;text-align: center;">
            <span>
                <button type="button" class="btn btn-default" @click="goBack">关闭</button>
            </span>
        </div>
</div>
<!-- /.modal-content -->
</div>
</template>
<script>
    /*
     * 照片对比结果
     * params: {modalProps: {imgRefUrl: 参照人脸照片url,confidence:人脸比对结果的置信度,comparedImgUrl:比对的图片的url列表}}
     */
    import action from '../../action/interface.js'
    import mixins from '../mixin.js'
    import dict from '../../js/dict.js'


    export default {
        props: ['modalProps']
        , data() {
            return { 'uname': '', 'idcard': '', 'mobile': '', 'remark': '', 'dict': dict,'compareImg':this.modalProps.comparedImgUrl[0],'nowNum':1 }
        }
        , components: {}

        , created() {
        }
        , mounted() {
            eventHub.$on('go-back', this.goBack)
        }
        , methods: {
            close() {
                eventHub.$emit('close-modal')
            }
            ,goBack() {//返回
                eventHub.$emit('close-modal')
            }
            ,goLeft() {
                let num = this.nowNum - 1;
                if(num > 0){
                    this.nowNum = this.nowNum  - 1;
                    this.compareImg = this.modalProps.comparedImgUrl[num-1]
                }
            }
            ,goRight() {
                let num = this.nowNum - 1;
                if(num < this.modalProps.comparedImgUrl.length-1){
                    this.nowNum = this.nowNum + 1;
                    this.compareImg = this.modalProps.comparedImgUrl[num+1]
                }
            }
        }
        , mixins: [mixins]
    }
</script>


