<style lang="scss">
    @import './header.scss'
</style>
<template>
    <div class="header">
        <div class="pull-left">
            <span class="nav-control" @click="toggleNav"><i class="icon-indent-left"></i> </span>
               
            <span class="sys-name">信贷APP后台</span>
        </div>
        <div class="pull-right">
            <img src="../../../images/face.png"/>
            <span>{{uname}}</span>
            <span><a href="/common/logout">退出</a></span>
        </div>  
    </div>
</template>
<script>
/*
 * 页头部分　
 * params: {uname}
 */
export default {
    props: ['uname']
    ,methods: {
        toggleNav () {
            eventHub.$emit('toggle-nav')
        }
    }
}
</script>


