<template>
<div style="text-align:center;">
    <form-group>
        <name-condition slot="input" v-model="uname"></name-condition>
        <mobile-condition slot="input" v-model="mobile"></mobile-condition>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">贷款编号</label>
            <div class="col-md-8">
                <input type="text" class="form-control" v-model="loanNo"/>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4">放款时间</label>
            <div class="col-md-8">
                <date-condition q-name="loanTime" q-class="form-control" v-model="loanTime"></date-condition>
            </div>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4 whp-n">产品类型：</label>
            <div class="col-md-8">
                <select class="form-control" v-model="pid">
                    <option value="">全部</option>
                    <option value="1">随想贷</option>
                    <option value="3">车贷</option>
                    <option value="2">微米贷</option>
                    <option value="4">多米贷</option>
                </select>
            </div>
        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <td></td>
                    <td>贷款编号</td>
                    <td>客户姓名</td>
                    <td>手机号码</td>
                    <td>身份证号</td>
                    <td>贷款账号</td>
                    <td>产品类型</td>
                    <td>贷款申请日期</td>
                    <td>贷款发放日期</td>
                    <td>贷款到期日</td>
                    <td>当前状态</td>
                    <td>还款方式</td>
                    <td>申请金额（元）</td>
                    <td>到账金额（元）</td>
                    <td>管理费（元）</td>
                    <td>已还本金（元）</td>
                    <td>已还利息（元）</td>
                    <td>已还罚息(元)</td>
                    <td>未还本金（元）</td>
                    <td>未还利息（元）</td>
                    <td>未还罚息（元）</td>
                    <td>操作</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{item.loanno}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.mobile}}</td>
                    <td>{{item.idcard}}</td>
                    <td>{{item.bankcard}}</td>
                    <td>{{item.productname}}</td>
                    <td>{{fDate(item.loanapplytime, 'yyyy-MM-dd')}}</td>
                    <td>{{fDate(item.loantime, 'yyyy-MM-dd')}}</td>
                    <td>{{fDate(item.endtime, 'yyyy-MM-dd')}}</td>
                    <td>{{getDictValue(item.status, dict.loanStatus)}}</td>
                    <td>{{getDictValue(item.paymentmethod, dict.paymentmethod)}}</td>
                    <td>{{fMoney(item.loanmoney)}}</td>
                    <td>{{fMoney(item.accountmoney)}}</td>
                    <td>{{fMoney(item.mngmoney)}}</td>
                    <td>{{fMoney(item.payedcapital)}}</td>
                    <td>{{fMoney(item.payedinterestmoney)}}</td>
                    <td>{{fMoney(item.payedpenaltymoney)}}</td>
                    <td>{{fMoney(item.restcapital)}}</td>
                    <td>{{fMoney(item.restinterestmoney)}}</td>
                    <td>{{fMoney(item.restpenaltymoney)}}</td>
                    <td><a href="javascript:;" @click="showRepayPlan(item.loanno,item.status);">查看还款计划</a>&nbsp;<span>|</span>&nbsp;<a href="javascript:;" @click="showRepayHis(item.loanno);">查看历史还款记录</a></td>
                </tr>
            </tbody>   
        </table>
              <!--暂无数据 start-->
        <div class="noData">
            {{errorInfo}}
        </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
    /*
     * 贷款管理主页
     */
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import page from '../basic/page/page.vue'
import repayPlan from './repay-plan.vue'


import action from '../../action/interface.js'
import mixin from '../mixin.js' 
import dict from '../../js/dict.js'

var pagesize = 8
export default {
    components: {nameCondition, mobileCondition, formGroup, queryBtnGroup, page, dateCondition,repayPlan}
    ,mixins: [mixin]
    ,data () {
        return {'mobile': '', 'uname': '','loanNo': '', 'loanTime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'dict': dict,'pid':''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.repayQuery
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'mobile': this.mobile, 'uname': this.uname, 'loanNo': this.loanNo, 'loanTime': this.loanTime,  'offset': _offset, 'pagesize': pagesize,'pid':this.pid}
        }
    }
    ,created () {
        this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="loanTime"]').datetimepicker().on('changeDate', function(ev){
            vm.loanTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
 
    }
    ,methods: {
        clearAccount (detail) {
            eventHub.$emit('open-modal', returnMoney, {'detail': detail})
        }
        //还款计划
        ,showRepayPlan (loanno,status) {
            this.$router.push({name: 'repayPlan',query:{'loanno':loanno,'status':status}})            
        }
         //历史还款记录
        ,showRepayHis (loanno) {
            this.$router.push({name: 'paymentHis',query:{'loanno':loanno}})       
        }
    }
}
</script>


