
<template>
    <div>
        <form-group>
            <div class="form-group col-md-3" slot="input">
                <label class="control-label col-md-4 whp-n">产品类型：</label>
                <div class="col-md-8">
                    <select class="form-control" v-model="id">
                        <option value="">全部</option>
                        <option value="1">随想贷</option>
                        <option value="3">车贷</option>
                        <option value="2">微米贷</option>
                        <option value="4">多米贷</option>
                    </select>
                </div>
            </div>
            <query-btn-group slot="btn"></query-btn-group>
        </form-group>
        <button type="button" class="btn btn-default btn-outline" @click="addNew"><i class="icon-plus"></i> &nbsp;新增</button>
        <button type="button" class="btn btn-default btn-outline" @click="showHistory"><i class="icon-search"></i> &nbsp;查看操作历史</button>
        <button type="button" class="btn btn-default btn-outline" @click="doQuery"><i class="icon-undo"></i> &nbsp;刷新</button>
        <div class="table-responsive">
            <table class="table table-bordered table-striped">
              <thead>
              <tr>
                <th>序号</th>
                <th>产品ID</th>
                <th>产品类型</th>
                <th>贷款利率与期限</th>
                <th>罚息利率</th>
                <th>最高贷款额度（元）</th>
                <th>最低贷款额度（元）</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item, index) in reDataList">
                <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                <td>{{item.id}}</td>
                <td>{{item.name}}</td>
                <td><a href="javascript:;" @click="showDetail(item.id)">详情</a></td>
                <td>{{toPercent(item.penaltyinterest)}}%</td>
                <td>{{fMoney(item.maxmoney, 2)}}</td>
                <td>{{fMoney(item.minmoney, 2)}}</td>
                <td>
                  <a v-if="item.hasvalid == 0" href="javascript:;" @click="validateProd(item.id)">启用</a>
                  <a v-else href="javascript:;" @click="validateProd(item.id)">停用</a>
                  <span v-if="item.name != '微米贷'">|</span>
                  <a v-if="item.name != '微米贷'" href="javascript:;" @click="editProd(item.id)">修改</a>
                </td>
              </tr>

              </tbody>
            </table>
          <!--暂无数据 start-->
            <div class="noData">
                {{errorInfo}}
            </div>
          <!--暂无数据 end-->
        </div>
        <page :page-info="pageInfo" :total="total"></page>
    </div>

</template>
<script>
/*
 * 产品管理主页面
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'

import page from '../basic/page/page.vue'

import addProd from './add-prod.vue'
import editProd from './edit-prod.vue'
import validateProd from './validate-prod.vue'
import deleteProd from './delete-prod.vue'
import detailProd from './detail-prod.vue'
import formGroup from '../form-group/form-group.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'

var pagesize = 8
export default {
    data () {
        return {'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'checkAll': 0,'id':''}
    }
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'offset': _offset, 'pagesize': pagesize,'id':this.id}
        }
        ,'url': function () { //请求地址
            return action.rootPath + action.interface.prodQuery
        }
    }
    ,components: {page, addProd, editProd, validateProd, deleteProd, detailProd,formGroup,queryBtnGroup}
    ,mixins: [mixins]
    ,created () {
        this.doQuery()
    }

    ,methods: {

        //打开历史产品修改记录
        showHistory () {
            this.$router.push({name: 'prodEditHis'})            
        }
        ,addNew () { //新增产品
            eventHub.$emit('open-modal', addProd, {'modalW': '800px'})
        }
        ,editProd (pid) {
            eventHub.$emit('open-modal', editProd, {'pid': pid, 'modalW': '800px'})
        }
        ,validateProd (pid) { //启用/停用
            eventHub.$emit('open-modal', validateProd, {'pid': pid})
        }
        ,showDetail (pid) {//显示利率详情
            eventHub.$emit('open-modal', detailProd, {'pid': pid})
        }
        ,deleteProd (pid) {//删除产品
            eventHub.$emit('open-modal', deleteProd, {'pid': pid})
        }
    }
    ,beforeDestroy () {
        //注销组件前，关闭所有弹窗
        eventHub.$emit('close-all-modal')
    }
}
</script>


