<template>
<div>
    <form-group>
        <div class="form-group col-md-5" slot="input">
            <label class="control-label">
              日期：
            </label>
            <date-condition q-name="startTime" v-model="startTime"></date-condition>
            <span slot="input">-</span>
            <date-condition q-name="endTime" v-model="endTime"></date-condition>
        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
      <table class="table table-bordered table-striped">
        <thead>
          <tr>
            <th></th>
            <th>T日期</th>
            <th>累计登录客户数</th>
            <th>今日登录客户数</th>
            <th>今日新增客户数</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, index) in reDataList">
              <td>{{(pageNum - 1) * 8 + index + 1}}</td>
              <td>{{row.rpdt}}</td>
              <td>{{row.logincottotal}}</td>
              <td>{{row.logincot}}</td>
              <td>{{row.logincotadd}}</td>
          </tr>
        </tbody>
      </table>
      <!--暂无数据 start-->
      <div class="noData">
        {{errorInfo}}
      </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
/*
 * 登录日志查询
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import dateCondition from '../form-group/date-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'


let pagesize = 8
export default {
    data () {
        return {'startTime': '', 'endTime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': ''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.queryLoginSummary
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'startTime': this.startTime, 'endTime': this.endTime, 'offset': _offset, 'pagesize': pagesize}
        }

    }
    ,components: {queryBtnGroup, dateCondition, formGroup, page}
    ,created () {
      this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="startTime"]').datetimepicker().on('changeDate', function(ev){
            vm.startTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
        $('input[name="endTime"]').datetimepicker().on('changeDate', function(ev){
            vm.endTime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });        
    }

    ,mixins: [mixins]
}
</script>


