
<template>
<div>
    <form-group>
        <name-condition slot="input" q-name="name" v-model="name"></name-condition>
        <mobile-condition slot="input" q-name="mobile" v-model="mobile"></mobile-condition>
        <uid-condition slot="input" q-name="uid" v-model="uid"></uid-condition>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
      <table class="table table-bordered table-striped">
        <thead>
          <tr>
            <th></th>
            <th>统计时间</th>
            <th>客户姓名</th>
            <th>手机号码</th>
            <th>客户ID</th>
            <th>最后登录时间</th>
            <th>累计登录次数</th>
            <th>手机系统</th>
            <th>手机型号</th>
            <th>最近5次IP地址记录</th>
            <th>IMEI号</th>
            <th>wifi MAC地址</th>
            <th>周围前5位wifi MAC地址</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, index) in reDataList">
              <td>{{(pageNum - 1) * 8 + index + 1}}</td>
              <td>{{timeNormal(row.rpdt)}}</td>
              <td>{{row.username}}</td>
              <td>{{row.mobile}}</td>
              <td>{{row.uid}}</td>
              <td>{{timeNormal(row.lstlogindt)}}</td>
              <td>{{row.logincottotal}}</td>
              <td>{{getDictValue(row.lstmobilesystem, dict.mobilesysterm)}}</td>
              <td>{{row.lstmobiletype}}</td>
              <td><a  href="javascript:;" @click="goIPHistory(row.uid)">查看</a></td>
              <td>{{row.lstimeicode}}</td>
              <td>{{row.lstwifimac}}</td>
              <td><a  href="javascript:;" @click="goLstWifiAroundMac(row.lstwifiaroundmac)">查看</a></td>
          </tr>
        </tbody>
      </table>
      <!--暂无数据 start-->
      <div class="noData">
        {{errorInfo}}
      </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
/*
 * 登录日志查询
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import uidCondition from '../form-group/uid-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'
import lwmList from './lwm-list.vue'
import IPHistory from './ip-history.vue'

let pagesize = 8
export default {
    data () {
        return {'name': '', 'mobile': '', 'uid': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', 'dict': dict}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.queryLoginLog
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'userName': this.name, 'mobile': this.mobile, 'uid': this.uid, 'offset': _offset, 'pagesize': pagesize}
        }
    }
    ,components: {queryBtnGroup, nameCondition, mobileCondition, uidCondition, formGroup, page,lwmList,IPHistory}
    ,created () {
        this.doQuery()
    }
    ,methods: {
        //查看周围前5位wifi MAC地址
        goIPHistory (uid) {
            eventHub.$emit('open-modal', IPHistory, {'modalW':'500px','uid':uid});
        }
        //查看周围前5位wifi MAC地址
        ,goLstWifiAroundMac (lwamList) {
            eventHub.$emit('open-modal', lwmList, {'modalW':'300px','lwamList':lwamList});
        }
    }
    ,mixins: [mixins]
}
</script>


