import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
//入口组件
// import App from './App'
//路由组件
// import Hello from './components/Hello.vue'

import main from './main.vue'

// import modal from './components/basic/modal/create-modal.vue'
import welcome from './components/welcome/welcome.vue'
import loanStatistics from './components/loan-statistics/loan-statistics.vue' 
import delayStatistics from './components/loan-statistics/delay-statistics.vue'
import creditManage from './components/credit-manage/credit-manage.vue'
import creditHistory from './components/credit-manage/history.vue'
import customerManagement from './components/customer-management/customer-management.vue' 

import loginQuery from './components/login-query/login-query.vue' 
import loginStatistics from './components/login-query/login-statistics.vue' 
import prodManage from './components/prod-manage/prod-manage.vue' 
import prodEditHis from './components/prod-manage/edit-history.vue' 

import customerDetail from './components/customer-management/customer-detail.vue' 
import whiteList from './components/white-list-manage/white-list-manage.vue' 
import whitelistHistory from './components/white-list-manage/white-list-history.vue'
import contractManage from './components/contract-manage/contract-manage.vue'
import authManage from './components/auth-manage/auth-manage.vue'
import authHis from './components/auth-manage/his-auth.vue'
//贷款管理
import loanManage from './components/loan-manage/loan-manage.vue'
//逾期管理
import delayManage from './components/delay-manage/delay-manage.vue'
//贷后管理
import postLoanManage from './components/post-loan-manage/post-loan-manage.vue'
//还款计划
import repayPlan from './components/post-loan-manage/repay-plan.vue'
//历史还款记录
import paymentHis from './components/post-loan-manage/payment-history.vue'
//操作日志分页查询
import logOperation  from './components/log-operation/log-operation.vue'
import orderDetail from './components/order-detail/order-detail.vue'


//自定义表单验证
//require('./js/customize-validate.js')
// import tabShow from './components/basic/tab-show.vue'
//安装路由
Vue.use(VueRouter)
Vue.use(VueResource)
//配置组件和路由映射
const routes = [
    { path: '/', redirect: 'welcome'}
    ,{ path: '/welcome', component: welcome, name: 'welcome'}
    //逾期统计
    ,{ path: '/delayStatistics', component: delayStatistics, name: 'delayStatistics'}
    //贷款统计
    ,{ path: '/loanStatistics', component: loanStatistics, name: 'loanStatistics'}
    //客户管理
    ,{ path: '/customerManagement', component: customerManagement, name: 'customerManagement'}
    ,{ path: '/customerDetail', component: customerDetail, name: 'customerDetail'}
    //授信管理
    ,{ path: '/creditManage', component: creditManage, name: 'creditManage'}
    ,{ path: '/creditHistory', component: creditHistory, name: 'creditHistory'}
    //个人登录日志查询
    ,{ path: '/loginQuery', component: loginQuery, name: 'loginQuery'}
    //登录日志汇总
    ,{ path: '/loginStatistics', component: loginStatistics, name: 'loginStatistics'}
    //产品管理
    ,{ path: '/prodManage', component: prodManage, name: 'prodManage'}
    ,{ path: '/prodEditHis', component: prodEditHis, name: 'prodEditHis'}
    //白名单
    ,{ path: '/white/query', component: whiteList, name: 'whiteList'}
    //白名单历史
    ,{ path: '/whitelistHistory', component: whitelistHistory, name: 'whitelistHistory'}
    //合同管理
    ,{ path: '/contractManage', component: contractManage, name: 'contractManage'}
    //授权管理
    ,{ path: '/authManage', component: authManage, name: 'authManage'}
    ,{ path: '/authHis', component: authHis, name: 'authHis'}
    //贷款管理
    ,{ path: '/loanManage', component: loanManage, name: 'loanManage'}
    //逾期管理
    ,{ path: '/delayManage', component: delayManage, name: 'delayManage'}
    //贷后管理
    ,{ path: '/postLoanManage', component: postLoanManage, name: 'postLoanManage'}
    //还款计划
    ,{ path: '/repayPlan', component: repayPlan, name: 'repayPlan'}
    //历史还款记录
    ,{ path: '/paymentHis', component: paymentHis, name: 'paymentHis'}
    //操作日志分页查询
    ,{ path: '/logOperation', component: logOperation, name: 'logOperation'}
    //详情页
    ,{ path: '/orderDetail', component: orderDetail, name: 'orderDetail'}

]

const router = new VueRouter({
    routes
    //,mode: 'history'
})

/* eslint-disable no-new */
// const main = new Vue({
//   router

const eventHub = new Vue()
//设置全局变量
window.eventHub = eventHub
// }).$mount('#main')
new Vue({
    el: "#main"
    ,router
    ,render: h => h(main)
})

// new Vue({
//     el: "#modal"
//     ,render: h => h(modal)
// })
