<template>
    <ul class="col-xs-12">
        <div class="infoTab">
          <div class="title col-xs-12">通讯录信息</div>
          <div class="zmxy" v-if="reDataList">
            芝麻信用分数：{{reDataList}}分
          </div>
          <div class="zmxy" v-else>
            芝麻信用分数：{{errorInfo}}
          </div>
        </div>
      </ul>
</template>

<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {

           'qParams': function () {
            return {id:this.id,uname:this.$route.query.uname,idCard:this.$route.query.idcard}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.zmfQuery
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()

       }
  }
</script>


