<template>
  <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条总额度：</label>
                  <span>{{reDataList.creditlimit}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条可用额度：</label>
                  <span>{{reDataList.touravailablelimit}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>总付款金额：</label>
                  <span>{{reDataList.tourcreditlimit}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条欠款：</label>
                  <span>{{reDataList.creditwaitpay}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条逾期：</label>
                  <span>{{reDataList.delinquencybalance}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条循环可用额度：</label>
                  <span>{{reDataList.tourcreditlimit}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条循环总额度：</label>
                  <span>{{reDataList.tourcreditlimit}}元</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>白条循环逾期：</label>
                  <span>{{reDataList.tourdelinquencybalance}}元</span>
                </p>
              </div>
            </div>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.jdWealth
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



