<style>
  .v-error{
    border-color:red; 
  }
</style>
<template>
  <div class="infoTab">
    <div class="title col-xs-12">审核意见</div>
    <div class="formInfo">
      <div class="col-xs-12 clearfix">
        <div class="col-xs-6">
          <label>{{labelId}}意见：</label>
          <select class=" input-sm input-normal" name="status" id="status">
            <option selected="selected">请选择</option>
            <option class="ct">通过</option>
            <option class="ct">未通过</option>
          </select>
        </div>
      </div>
      <div class="col-xs-12 clearfix areaTxt">
        <label class="fl">{{labelId}}结论：</label>
        <textarea class="form-control fl remark" rows="3" v-model="detail"></textarea>
      </div>
      <div class="col-xs-12 tc">
        <button type="button" class="btn btn-default" @click="onSubmit">提交</button>
      </div>
    </div>
  </div>
</template>

<script>
/*
 * 提交审核意见
 */
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import Vue from 'vue'

var retrialUrl = action.rootPath + action.interface.retrial
var firstTrialUrl = action.rootPath + action.interface.firstTrial
var url
export default {
        data () {
        return {'routeId':this.$route.query.routeId,'detail':'','labelId':''}
      }
        ,props: ["id","loanNo"]
        ,computed: {
            'params': function () {
                let status = $("#status").val()
                if(status=="通过"){
                  status = 1
                }else{
                  status = 2
                }
                return {
                  'detail': this.detail, 'status':status, 'taskId': this.$route.query.id, 'loanNo':this.$route.query.loanNo}
            }
        }
        ,created () {
        var routeId = this.$route.query.routeId;
        if(routeId==1){
            url = firstTrialUrl;
            this.labelId = '初审'
        }else if(routeId==3){
            url = retrialUrl;
            this.labelId = '复核'
        }
        }
        ,methods: {
            //提交
            onSubmit () {
                    let _params = this.params
                    let _this = this
                    let pushName 
                    if(this.routeId==1){
                        pushName = 'cswaitOrder'
                    }else if(this.routeId==3){
                        pushName = 'fswaitOrder'
                    }
                    if($("#status .ct").is(":checked")){
                      let txtVal = $(".remark").val();
                      if(txtVal){
                        $(".remark").removeClass("v-error");
                        this.post(url, _params, function (res) {
                          if (res.errorCode == 0) {
                              eventHub.$emit('show-alert', '提交成功！')
                              _this.$router.push({name: pushName})     
                          } else {
                              eventHub.$emit('show-alert', res.message)      
                          }
                        }) 
                      }else{
                        $(".remark").addClass("v-error");
                      }
                    }else{
                      eventHub.$emit('show-alert', '请选择审核意见')
                    }
                    
                  
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
  }
</script>


