<template>
<div class="infoTab">
    <div class="title col-xs-12">贷款信息</div>
 <div class="col-xs-12 info clearfix">
      <div class="col-xs-4">
        <p>
          <label>客户姓名：</label>
          <span>{{reDataList.uname}}</span>
        </p>
        <p>
          <label>贷款期限：</label>
          <span>{{reDataList.loanDay}}</span><span v-if="reDataList.termType==0">天</span><span v-if="reDataList.termType==1">月</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>身份证号：</label>
          <span>{{reDataList.idCard}}</span>
        </p>
        <p>
          <label>贷款日利率：</label>
          <span>{{toPercent(reDataList.dailyRate)}}</span>%
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>贷款金额：</label>
          <span>{{reDataList.loanMoney}}</span>元
        </p>
        <p>
          <label>还款方式：</label>
          <span>{{getDictValue(reDataList.paymentMethod, dict.paymentMethod)}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label>产品类型：</label>
          <span>{{reDataList.productName}}</span>
        </p>
      </div>
    </div>
     </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import dict from '../../js/dict.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': '','dict': dict}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id,'loanNo':this.$route.query.loanNo}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.queryLoanInfo
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>


