<style lang="scss">
    @import '../white-list-manage/alert-whitelist.scss';
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">修改额度</h4>
        </div>
        <div class="modal-body">
            <form action="#" method="post" id="changeCrdForm" class="regForm">
                <div class="col-xs-12 form-inline group-left validateModal">
                    <div class="form-group col-xs-12 clearfix">
                        <p>
                            <label for="" class="fl">客户姓名：</label>{{modalProps.uname}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <p>
                            <label for="" class="fl"> 客户ID：</label>{{modalProps.uid}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <p>
                            <label for="" class="fl">修改前总额度：</label>{{modalProps.creditmoney}}</p>
                    </div>
                    <div class="form-group col-xs-12">
                        <label>额度修改为：</label>
                        <input type="text" class="form-control" style="width:150px;" name="newCreditmoney" v-model="newCreditmoney" required/> 元
                    </div>
                </div>
            </form>
            <div class="modal-footer">
                <save-back-btn></save-back-btn>
            </div>
        </div>
        <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 修改额度
 * params: {modalProps: {id: 记录id，uname: 客户姓名，creditmoney: 修改前总额度}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import saveBackBtn from '../query-btn-group/save-back-btn.vue'

var creditUpdate = action.rootPath + action.interface.creditUpdate
    export default {
        props: ['modalProps']
        ,data () {
            return {}
        }
        ,components: {saveBackBtn}

        ,created () {
            
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
            $('#changeCrdForm').validate({
            rules: {
                'newCreditmoney': {
                    'number': true
                    ,'min': 0
                }
            }
        })
        }        
        ,methods: {
            //保存
            doSave () {
                if ($('#changeCrdForm').valid()){
                    let _params = {'id': this.modalProps.id,'creditmoney':this.newCreditmoney}
                    this.post(creditUpdate, _params, function (res) {
                        if(res.code == 403){
                            eventHub.$emit('show-alert', res.errorMsg);
                        }else if (res.errorcode == 0) {
                            eventHub.$emit('show-alert', '保存成功');
                            $('#changeCrdForm')[0].reset();
                        } else {
                            eventHub.$emit('show-alert', res.message)
                        }
                    })                    
                }
            }
            ,close () {
                eventHub.$emit('close-modal')
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


