<template>
  <div class="infoTable">
            <table class="table table-bordered">
              <thead>
              <tr>
                <th>收货人</th>
                <th>电话号码</th>
                <th>邮编</th>
                <th>地址</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for='item in reDataList'>
                <td>{{item.name}}</td>
                <td>{{item.phonenumber}}</td>
                <td></td>
                <td>{{item.address}}{{item.fulladdress}}</td>
              </tr>
              </tbody>
            </table>
            <div class="noData">
                {{errorInfo}}
            </div>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
              return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.jdAddress
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



