<template>
    <div class="modal-content">
      <div class="modal-header">
        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
        <h4 class="modal-title">删除产品</h4>
      </div>
      <div class="modal-body">
        <form id="deleteProd" class="regForm">
          <div class="col-xs-12 form-inline group-left validateModal">
            <div class="form-group col-xs-12 clearfix">
                <label>产品类型：</label><span>{{name}}</span>
            </div>
            <div class="form-group col-xs-12">
                <label> 产品ID：</label><span>{{id}}</span>
            </div>
            <div class="form-group col-xs-12">
                <label>当前状态：</label> <span>{{getDictValue(hasvalid, dict.operateStatus)}}</span>
            </div>
            <div class="form-group col-xs-12">
              <label>备注：</label>
              <textarea class="note" v-model="remark"></textarea>
            </div>
          </div>
        </form>
        </div>
        <div class="modal-footer">
          <save-back-btn></save-back-btn>
        </div>
      </div>
      <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 删除产品
 * params: {modalProps: {pid}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import saveBackBtn from '../query-btn-group/save-back-btn.vue'

var queryProd = action.rootPath + action.interface.prodOne
var saveAction = action.rootPath + action.interface.prodDelete
    export default {
        props: ['modalProps']
        ,data () {
            return {'name': '', 'id': '', 'hasvalid': 0, 'dict': dict, 'remark': ''}
        }
        ,components: {saveBackBtn}

        ,created () {
            //请求详情内容
            let vm = this
            vm.send(queryProd, {'id': vm.modalProps.pid}, function (res) {
                if (res.errorcode == 0) {
                    vm.name = res.result.name
                    vm.id = res.result.id
                    vm.hasvalid = res.result.hasvalid
                }
            })
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
        }
        ,watch: {
            'modalProps': function (newV) {
                //请求详情内容
                let vm = this
                vm.send(queryProd, {'id': vm.modalProps.pid}, function (res) {
                    if (res.errorcode == 0) {
                        vm.name = res.result.name
                        vm.id = res.result.id
                        vm.hasvalid = res.result.hasvalid
                    }
                })
            } 
        }
        ,methods: {

            //保存
            doSave () {
                if ($('#deleteProd').valid()){
                    let _params = {'id': this.modalProps.pid, 'remark': this.remark}
                    this.post(saveAction, _params, function (res) {
                        if (res.errorcode == 0) {
                            eventHub.$emit('show-alert', '保存成功！')
                        } else {
                            eventHub.$emit('show-alert', res.message)
                        }
                    })                    
                }

            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


