<style>
    .lwm-list {
        padding: 0;
    }
    .lwm-list li{
        height: 40px;
        line-height: 40px;
        border-bottom:1px #ccc solid; 
        list-style: none;
    }
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">最近五次IP地址记录</h4>
        </div>
        <div class="modal-body">
            <form action="#"  class="regForm">
                <div class="col-xs-12 group-left validateModal loan-alert clearfix" v-if="ipList.length > 0">
                    <ul class="col-xs-6 clearfix lwm-list">
                        <p>登录时间</p>
                        <li v-for="item in ipList">{{item.logontime}}</li>
                    </ul>
                    <ul class="col-xs-6 clearfix lwm-list">
                        <p>IP地址</p>
                        <li v-for="item in ipList">{{item.ip}}</li>
                    </ul>
                </div>
                <p v-else class="tc">暂无数据</p>
            </form>
            
            <div class="modal-footer">
                 <span>
                    <button type="button" class="btn btn-default" @click="goBack">关闭</button>
                </span>
            </div>
        </div>
        <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 最近五次IP地址记录
 * params: {modalProps: {uid: 客户id}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


var queryIPHistory = action.rootPath + action.interface.queryIPHistory
    export default {
        props: ['modalProps']
        ,data () {
            return {'uname': '', 'idcard': '', 'mobile': '', 'remark': '','dict': dict,'ipList':[]}
        }
        ,components: {}

        ,created () {
            //请求详情内容
            let vm = this
            vm.send(queryIPHistory, {'uid': vm.modalProps.uid}, function (res) {
                if (res.errorcode == 0) {
                    vm.ipList = res.result
                }
            })
        }
        ,mounted () {
            eventHub.$on('go-back', this.goBack)
        }        
        ,methods: {
            close () {
                eventHub.$emit('close-modal')
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


