
<template>
    <div class="infoTab">
        <div class="title col-xs-12">产品操作历史</div>
        <div class="infoTable">
            <button type="button" class="btn btn-default btn-outline" @click="goBack">返回</button>
            <div class="table-responsive">
                <table class="table table-bordered table-striped">
                  <thead>
                  <tr>
                    <th>序号</th>
                    <th>产品ID</th>
                    <th>产品类型</th>
                    <th>贷款利率与期限</th>
                    <th>罚息利率</th>
                    <th>最高贷款额度（元）</th>
                    <th>最低贷款额度（元）</th>
                    <th>操作时间</th>
                    <th>备注</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{item.pid}}</td>
                    <td>{{item.name}}</td>
                    <td><a href="javascript:;" @click="showDetail(item.id)">详情</a></td>
                    <td>{{toPercent(item.penaltyinterest)}}</td>
                    <td>{{fMoney(item.maxmoney)}}</td>
                    <td>{{fMoney(item.minmoney)}}</td>
                    <td>{{fDate(item.createtime, 'yyyy-MM-dd hh:mm:ss')}}</td>
                    <td>{{item.remark}}</td>
                  </tr>
                  </tbody>
                </table>
            </div>
        </div>
        <page :pageInfo="pageInfo" :total="total"></page>
    </div>
</template>
<script>
/*
 * 产品修改历史
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'

import page from '../basic/page/page.vue'
import detailProd from './detail-prod.vue'
//历史详情查询地址
var qOperateHis = action.rootPath + action.interface.queryOperation
var pagesize = 8
export default {
    data () {
        return {'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': ''}
    }
    ,components: {page, detailProd}
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'uname': this.uname, 'mobile': this.mobile, 'offset': _offset, 'pagesize': pagesize}
        }
        , 'url': function () { //请求地址
            return action.rootPath + action.interface.queryOperations
        }
    }
    ,created () {
        this.doQuery()
        //监听跳转第几页
        eventHub.$on('go-page', this.goPage)
    }
    ,methods: {
        goBack () { //返回
            this.$router.go(-1)
        }
        ,showDetail (pid) {
            eventHub.$emit('open-modal', detailProd, {'pid': pid, 'url': qOperateHis});
        }
    }
    ,mixins: [mixins]
}
</script>


