<style>
    dt, dd {
       line-height: 40px; 
    }
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">结清贷款</h4>
        </div>
        <div class="modal-body">
            <dl class="dl-horizontal">
                <dt>客户姓名:</dt><dd>{{detail.uname}}</dd>
                <dt>客户ID:</dt><dd>{{detail.uid}}</dd>
                <dt>贷款编号:</dt><dd>{{detail.loanno}}</dd>
                <dt>当前状态:</dt><dd>{{getDictValue(detail.status, dict.loanStatus)}}</dd>
            </dl>
            <div class="text-center text-danger"><strong>提示：一旦结清，无法撤销，请确认是否结清？</strong></div>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="checkClear">确认结清</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
        </div>
    </div>
</template>
<script>
/*
 * 结清贷款操作
 * param {modal-props: {detail}}
 */
import action from '../../action/interface.js'
import mixin from '../mixin.js'
import dict from '../../js/dict.js'

    export default {
        props: ['modalProps']
        ,computed: {
            'detail': function () {
                if (this.modalProps != undefined  && typeof(this.modalProps.detail) == 'object') return this.modalProps.detail
                else {
                    return {}
                }
            }
        }
        ,data () {
            return {dict}
        }
        ,methods: {
            checkClear () {
                let _url = action.rootPath + action.interface.loanUpdate
                let vm = this
                vm.post(_url, {'id': vm.detail.id, 'status': 5}, function (res) {
                    if (res.errorcode == 0) {
                        eventHub.$emit('show-alert', '保存成功！')
                    } else {
                        eventHub.$emit('show-alert', res.message)
                    }
                })                 
            }
        }
        ,mixins: [mixin]
    }
</script>


