<template>
  <div id="container">
  <div>
    <div class="col-xs-2 bs-docs-sidebar">
      <ul class="nav nav-list bs-docs-sidenav">
      <li v-for="(item, index) in tabList" :class="{active:item.iscur}" @click="setNav(index)"><a><i class="icon-chevron-right"></i>{{item.name}}</a></li>
      </ul>
    </div>
    <div class="col-xs-10 outInfo">
    <component :is="rightItem" :id="id"></component>
    </div>
  </div>

</div>
</template>
<script>
/*
 * 外部信息页面
 */
import action from '../../action/interface.js' 
//require('../../js/outInfo.js')
import mixins from '../mixin.js'
import Vue from 'vue'

import checkNav from '../check-tabs/check-nav.vue'
import zmf from '../check-tabs/zmf.vue'
import taobao from '../check-tabs/taobao.vue'
import zhifubao from '../check-tabs/zhifubao.vue'
import jingdong from '../check-tabs/jingdong.vue'
import carrieroperator from '../check-tabs/carrieroperator.vue'

export default {
    props: ["id"]
    ,data () {
        //uidList: 保存选择的用户ID 
        return {'rightItem' :'zmf','tabList':[
        {name:'芝麻信息',iscur:true,id:'zmf'},
        {name:'淘宝信息',iscur:false,id:'taobao'},
        {name:'支付宝信息',iscur:false,id:'zhifubao'},
        {name:'京东信息',iscur:false,id:'jingdong'},
        {name:'运营商信息',iscur:false,id:'carrieroperator'}]}
    }
    ,components: {zmf,taobao,jingdong,carrieroperator,checkNav,zhifubao}
    ,mixins: [mixins]
    ,created () {
        //this.queryCreditedList();
        //eventHub.$on('get-index', this.getIndex);
    }
    ,methods: {
        setNav: function (index) {
         this.tabList.map(function (v,i) {
         i==index? v.iscur=true: v.iscur=false;
         });
         this.rightItem = this.tabList[index].id;
        }
        
        
    }
}
</script>


