<template>
<div>
    <form-group>
        <div class="form-group col-md-6" slot="input">
            <label class="control-label">
              日期：
            </label>
            <date-condition q-name="starttime" v-model="starttime"></date-condition>
            <span slot="input">-</span>
            <date-condition q-name="endtime" v-model="endtime"></date-condition>
        </div>
        <div class="form-group col-md-3" slot="input">
            <label class="control-label col-md-4 whp-n">产品类型：</label>
            <div class="col-md-8">
                <select class="form-control" v-model="pid">
                    <option value="">全部</option>
                    <option value="1">随想贷</option>
                    <option value="3">车贷</option>
                    <option value="2">微米贷</option>
                    <option value="4">多米贷</option>
                </select>
            </div>

        </div>
        <query-btn-group slot="btn"></query-btn-group>
    </form-group>
    <div class="table-responsive">
      <table class="table table-bordered table-striped">
        <thead>
        <tr>
          <th>日期</th>
          <th>产品类型</th>
          <th>T日放款金额</th>
          <th>累计放款金额</th>
          <th>T日管理费</th>
          <th>累计管理费</th>
          <th>T日贷款客户数</th>
          <th>T日新增客户数</th>
          <th>累计贷款客户数</th>
          <th>T日还款金额</th>
          <th>T日还款本金</th>
          <th>T日还款利息</th>
          <th>T日还款罚息</th>
          <th>T日其他费用</th>
          <th>T日垫资金额</th>
          <th>T日垫资收回金额</th>
          <th>累计还款金额</th>
          <th>累计还款利息</th>
          <th>累计还款罚息</th>
          <th>累计其他费用</th>
          <th>累计垫资金额</th>
          <th>累计垫资收回金额</th>
          <th>累计未收回垫资金额</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="row in reDataList">
          <td>{{fDate(row.rpdt, 'yyyy-MM-dd')}}</td>
          <td>{{getDictValue(row.prdtype, dict.prdtypeValue)}}</td>  
          <td>{{fMoney(row.borramt)}}</td>
          <td>{{fMoney(row.borramttotal)}}</td>
          <th>{{fMoney(row.manfee)}}</th>
          <th>{{fMoney(row.manfeetotal)}}</th>
          <td>{{row.borrcot}}</td>
          <td>{{row.borrcotadd}}</td>
          <td>{{row.borrcottotal}}</td>
          <td>{{fMoney(row.repayedamt)}}</td>
          <td>{{fMoney(row.repayedcap)}}</td>
          <td>{{fMoney(row.repayedint)}}</td>
          <td>{{fMoney(row.repayedlateint)}}</td>
          <td>{{fMoney(row.repayedothers)}}</td>
          <th>{{fMoney(row.lnamt)}}</th>
          <th>{{fMoney(row.lnbkamt)}}</th>
          <td>{{fMoney(row.repayedamttotal)}}</td>
          <td>{{fMoney(row.repayedinttotal)}}</td>
          <td>{{fMoney(row.repayedlateinttotal)}}</td>
          <td>{{fMoney(row.repayedotherstotal)}}</td>
          <th>{{fMoney(row.lnamttotal)}}</th>
          <th>{{fMoney(row.lnbkamttotal)}}</th>
          <th>{{fMoney(row.nobkamttotal)}}</th>
        </tr>
        </tbody>
      </table>
      <!--暂无数据 start-->
      <div class="noData">
        {{errorInfo}}
      </div>
      <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
</div>
</template>
<script>
/*
 * 贷款统计
 */
import action from '../../action/interface.js'
import mixin from '../mixin.js'
import dict from '../../js/dict.js'

import dateCondition from '../form-group/date-condition.vue'
import formGroup from '../form-group/form-group.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import page from '../basic/page/page.vue'


let aParams = {'starttime': '', 'endtime': '', 'offset': 0, 'pagesize': 8}
export default {
    data () {
        return {'starttime': '', 'endtime': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '',dict,'pid':''}
    }
    ,computed: {
        'pageInfo': function () {
            return {'pageSize': aParams.pagesize, 'currentpage': this.pageNum}
        }
        ,'url': function () {
            return action.rootPath + action.interface.sta
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, aParams.pagesize)
            return {'starttime': this.starttime, 'endtime': this.endtime, 'offset': _offset, 'pagesize': 8,'pid':this.pid}
        }
    }
    ,components: {dateCondition, formGroup, queryBtnGroup, page}
    ,mixins: [mixin]
    ,created () {
      this.doQuery()
    }
    ,mounted () {
        let vm = this
        $('input[name="starttime"]').datetimepicker().on('changeDate', function(ev){
            vm.starttime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')  
        });
        $('input[name="endtime"]').datetimepicker().on('changeDate', function(ev){
            vm.endtime = vm.fDate(ev.date.valueOf(), 'yyyy-MM-dd')
        });  
    }
}
</script>


