<style>
.border-line {
    border-bottom: 1px dashed #ddd;
    padding-bottom: 10px;
}
.right-item {
    position: absolute;
    right: 0;
    top: 0;
}
.center-item {
    position: absolute;
    right: 170px;
    top: 0;    
}
</style>
<template>
    <div class="modal-content">
         <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">利率详情</h4>
          </div>
          <div class="modal-body">
            <form class="regForm">
              <div class="col-xs-12 form-inline">
                <div class="form-group col-xs-12 clearfix">
                  <p>
                    <label>产品类型：</label>{{name}}</p>
                </div>
                <div class="form-group col-xs-12">
                  <p>
                    <label> 产品ID：</label>{{id}}</p>
                </div>
                <div class="form-group col-xs-12" v-for="item in interestinfos">
                  <div class="border-line">
                    <label class="control-label">
                      贷款期限：
                    </label>
                    {{item.term + getDictValue(item.termtype, dict.termType)}}
                  </div>
                  <div class="center-item" v-if="name != '微米贷'">
                    <label class="control-label">
                      利率：
                    </label>
                        {{toPercent(item.interest) + '%'}}
                  </div>
                    <div class="right-item" v-if="name != '微米贷'">
                    <label class="control-label">
                      管理费：
                    </label>
                        {{toPercent(item.mngrate) + '%'}}
                  </div>
                </div>
              </div>
            </form>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">确定</button>
            </div>
        </div>
          <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 产品详情
 * 
 * params : {modalProps: {pid, url(url为空时，取默认.　只要用于历史详情里接口地址不同)}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


    export default {
        props: ['modalProps']
        ,data () {
            return {'name': '', 'id': '','penaltyinterest': '', 'maxmoney': '', 'minmoney': '', 'interestinfos': [], 'dict': dict}
        }
        ,created () {
            //请求详情内容
            this.getData();
        }
        ,watch: {
            'modalProps': function (newV) {
                //请求详情内容
                this.getData();
            } 
        }
        ,methods: {
            getData () {
                var queryProd = action.rootPath + action.interface.prodOne
                //请求详情内容
                let vm = this
                let url = vm.modalProps.url
                if (url == '' || url == undefined) {
                    //url不存在时，取queryProd
                    url = queryProd
                }
                vm.send(url, {'id': vm.modalProps.pid}, function (res) {
                    if (res.errorcode == 0) {
                        vm.name = res.result.name
                        vm.id = res.result.id
                        vm.penaltyinterest = res.result.penaltyinterest
                        vm.maxmoney = res.result.maxmoney
                        vm.minmoney = res.result.minmoney
                        vm.interestinfos = res.result.interestinfos
                    }
                })
            }
        }
        ,mixins: [mixins]
    }
</script>


