<template>
  <div class="infoTable">
            <div class="visible-on pd10 grayBg clearfix">
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>姓名：</label>
                  <span>{{reDataList.realname}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>性别：</label>
                  <span>{{reDataList.sex}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>昵称：</label>
                  <span>{{reDataList.nick}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>身份证：</label>
                  <span>{{reDataList.idcard}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>手机号：</label>
                  <span>{{reDataList.phonenumber}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>邮箱：</label>
                  <span>{{reDataList.email}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>注册时间：</label>
                  <span>{{fDate(reDataList.registertime,'yyyy-MM-dd')}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>绑定微博：</label>
                  <span>{{reDataList.weibonick}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>成长值：</label>
                  <span>{{reDataList.growthvalue}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>等级：</label>
                  <span>{{reDataList.viplevel}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>信用分数：</label>
                  <span>{{reDataList.creditscore}}</span>
                </p>
              </div>
              <div class="col-xs-6 col-sm-4">
                <p>
                  <label>天猫经验值：</label>
                  <span>{{reDataList.tmvlaue}}</span>
                </p>
              </div>
            </div>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.taobaoBase
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



