<template>
  <div class="infoTable">
    <div class="form-group clearfix">
      <span class="balance"> 当前话费余额：</span><span v-if="reDataList.availablebalance">{{Number(reDataList.availablebalance)/100}} 元</span>
    </div>
    <div class="visible-on pd10 grayBg clearfix">
      <div class="col-xs-6 col-sm-4">
        <p>
          <label>姓名：</label>
          <span>{{reDataList.name}}</span>
        </p>
      </div>
      <div class="col-xs-6 col-sm-4">
        <p>
          <label>可用积分：</label>
          <span></span>
        </p>
      </div>
      <div class="col-xs-6 col-sm-4">
        <p>
          <label>客户星级：</label>
          <span>{{reDataList.level}}</span>
        </p>
      </div>
      <div class="col-xs-6 col-sm-4">
        <p>
          <label>手机号：</label>
          <span>{{reDataList.mobile}}</span>
        </p>
      </div>
      <div class="col-xs-6 col-sm-4">
        <p>
          <label>手机套餐名称：</label>
          <span>{{reDataList.packagename}}</span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.yysMobile
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>



