<style lang="scss">
   @import "./customer.scss";
</style>
<template>
    <div>
        <form-group>
            <name-condition slot="input" key="uname" v-model="uname"></name-condition>
            <mobile-condition slot="input" key="mobile" v-model="mobile"></mobile-condition>
            <uid-condition slot="input" key="uid" v-model="uid"></uid-condition>
            <query-btn-group slot="btn" ></query-btn-group>            
        </form-group>
<div class="mt50">
        <table class="table table-bordered table-striped">
          <thead>
          <tr>
            <th>序号</th>
            <th>客户ID</th>
            <th>客户姓名</th>
            <th>身份证号码</th>
            <th>电话号码</th>
            <th>详情</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(row,index) in reDataList">
            <td>{{(pageNum - 1) * 8 + index + 1}}</td>
            <td>{{row.uid}}</td>
            <td>{{row.uname}}</td>
            <td>{{row.idcard}}</td>
            <td>{{row.mobile}}</td>
            <td>
              <a href="javascript:;" @click="addTab(row.uid,row.uname,row.idcard)">详情</a>
            </td>
          </tr>
          
          </tbody>
        </table>
          <!--暂无数据 start-->
            <div class="noData">
                {{errorInfo}}
            </div>
          <!--暂无数据 end-->
      </div>

    <page :page-info="pageInfo" :total="total"></page>
    </div>
</template>
<script>
/*
 * 客户管理
 */
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import page from '../basic/page/page.vue'
import formGroup from '../form-group/form-group.vue'
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import uidCondition from '../form-group/uid-condition.vue'
import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import orderDetail from '../order-detail/order-detail.vue'


var pagesize = 8
export default {
    data () {
        return {'uname': '', 'mobile': '','uid': '', 'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': ''}
    }
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'uname': this.uname,'mobile': this.mobile, 'uid': this.uid, 'offset': _offset, 'pagesize': pagesize}
        }
        ,'url': function () { //请求地址
            return action.rootPath + action.interface.query
        }

    }
    ,components: {formGroup, nameCondition, mobileCondition, uidCondition, queryBtnGroup, page,orderDetail}
    ,mixins: [mixins]
    ,created () {
        this.doQuery()
    }
    ,methods: {
        addTab ( id ,uname ,idcard) {
                this.$router.push({name: 'orderDetail',query: {id: id,uname:uname,idcard:idcard}})
        }
        //
        // addTab ( uid ,id ) {
        //         //获取当前客户的ID
        //         console.log(id);
        //         // tab-nav
        //         //eventHub.$emit('add-tab', id, "客户详情")
        //         //eventHub.$on('add-tab', this.addTab)
        //         //eventHub.$on('add-tab', id, "客户详情")
        //         //change route
        //         this.$router.push({name: 'customerDetail', params: {uid: uid}, query: {id: id}})
        //          //this.$router.push("/customerDetail?tabId="+id);
        //            console.log(this.$router);
        //             //this.$router.push({'tabId': "customerDetail", 'title': "客户详情"});
        // },
    }
}
</script>


