<style lang="scss">
    @import './order-detail.scss';
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">修改详情</h4>
        </div>
        <div class="modal-body ocrinfo">
            <table class="table table-bordered table-striped">
                <thead>
                    <tr>
                        <td></td>
                        <td>OCR识别</td>
                        <td>修改后</td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>姓名</td>
                        <td>{{modalProps.customername}}</td>
                        <td>{{modalProps.editedname}}</td>
                    </tr>
                     <tr>
                        <td>身份证</td>
                        <td>{{modalProps.idcard}}</td>
                        <td>{{modalProps.editedidcardnum}}</td>
                    </tr>

                </tbody>
            </table>
        </div>
        <div class="modal-footer" style="margin-top:0;text-align: center;">
<span>
                    <button type="button" class="btn btn-default" @click="goBack">关闭</button>
                </span>
</div>
</div>
<!-- /.modal-content -->
</div>
</template>
<script>
    /*
     * 查看修改详情
     * params: {modalProps: {id: 记录id}}
     */
    import action from '../../action/interface.js'
    import mixins from '../mixin.js'
    import dict from '../../js/dict.js'


    export default {
        props: ['modalProps']
        , data() {
            return { 'uname': '', 'idcard': '', 'mobile': '', 'remark': '', 'dict': dict }
        }
        , components: {}

        , created() {
        }
        , mounted() {
            console.log(this.modalProps.addbidList)
            eventHub.$on('go-back', this.goBack)
        }
        , methods: {
            close() {
                eventHub.$emit('close-modal')
            }
            , goBack() {//返回
                eventHub.$emit('close-modal')
            }
        }
        , mixins: [mixins]
    }
</script>



