<template>
    <div>
        <form-group>
            <name-condition slot="input" q-name="uname" v-model="uname"></name-condition>
            <mobile-condition slot="input" q-name="mobile" v-model="mobile"></mobile-condition>
            <query-btn-group slot="btn"></query-btn-group>
        </form-group>
        <a type="button" class="btn btn-default btn-outline mr15" @click="addWhitelist"><i class="icon-plus"></i> 新增白名单</a>
        <a type="button" class="btn btn-default btn-outline mr15" @click="goHistory('111')"><i class="icon-search"></i> 操作历史</a>
        <a type="button" class="btn btn-sm btn-default btn-outline" @click="addExcel"><i class="icon-minus"></i> 批量导入</a>
        <div class="mt10">
          <table class="table table-bordered table-striped">
            <thead>
            <tr>
              <th>客户姓名</th>
              <th>身份证号码</th>
              <th>电话号码</th>
              <th>初始总额度（元）</th>
              <th>初始额度有效期（天）</th>
              <th>续贷最小还款比例（%）</th>
              <th>操作</th>
            </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in reDataList">
                  <td>{{item.uname}}</td>
                  <td>{{item.idcard}}</td>
                  <td>{{item.mobile}}</td>
                  <td>{{fMoney(item.creditmoney)}}</td>
                  <td>{{item.validterm}}</td>
                  <td>{{item.minrepayratio*100}}</td>
                  <td>
                    <a href="javascript:;" @click="updateWhitelist(item.id)">更新白名单</a> |
                    <a href="javascript:;" @click="removeWhitelist(item.id)">移除白名单</a>
                  </td>
                </tr>
            </tbody>
          </table>
          <!--暂无数据 start-->
            <div class="noData">
                {{errorInfo}}
            </div>
          <!--暂无数据 end-->
    </div>
    <page :page-info="pageInfo" :total="total"></page>
    </div>

</template>
<script>
/*
 * 白名单管理页面
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import Vue from 'vue'

import queryBtnGroup from '../query-btn-group/query-btn-group.vue'
import nameCondition from '../form-group/name-condition.vue'
import mobileCondition from '../form-group/mobile-condition.vue'
import formGroup from '../form-group/form-group.vue'
import page from '../basic/page/page.vue'

import addWhitelist from './add-white-list.vue'
import removeWhitelist from './remove-white-list.vue'
import updateWhitelist from './update-white-list.vue'
import addExcel from './add-excel.vue'
import queryhistory from './white-list-history.vue'


var pagesize = 8
export default {
    data () {
        //uidList: 保存选择的用户ID 
        return {'uname': '', 'mobile': '', 'pageNum': 1, 'total': 0, 'uidList': [], 'reDataList': [], 'errorInfo': ''}
    }
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
           let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'uname': this.uname,'mobile': this.mobile, 'offset': _offset, 'pagesize': pagesize}
        }
        ,'url': function () { //请求地址
            return action.rootPath + action.interface.whiteQuery
        }
    }
    ,components: {queryBtnGroup, nameCondition, mobileCondition, formGroup, page, queryhistory,addWhitelist,removeWhitelist,addExcel,updateWhitelist}
    ,mixins: [mixins]
    ,created () {
        this.doQuery()

    }
    ,methods: {
        //新增白名单
        addWhitelist () {
            eventHub.$emit('open-modal', addWhitelist);
            //eventHub.$emit('show-alert', 'test');
        }
        //移除名单
        ,removeWhitelist (id) {
            eventHub.$emit('open-modal', removeWhitelist, {'id': id});
        }
        //更新名单
        ,updateWhitelist (id) {
            eventHub.$emit('open-modal', updateWhitelist, {'id': id});
        }
        //导入excel
        ,addExcel () {
            eventHub.$emit('open-modal', addExcel);
        }
        //打开历史授信
        ,goHistory (uid) {
            this.$router.push({name: 'whitelistHistory'})            
        }
    }
    ,beforeDestroy () {
        //注销组件前，关闭所有弹窗
        eventHub.$emit('close-all-modal')
    }
}
</script>


