<style lang="scss">
    @import './c-mould.scss';
</style>
<template>
    <div class="modal-content m-modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">合同管理</h4>
        </div>
        <div class="modal-body">
            <div class="m-info clearfix">
                <div class="m-title m-tc">借款申请及借款申请须知</div>
                <ul>
                    <li>申请信息</li>
                    <li><label>申请人姓名：</label>{{reDataList.uname}}</li>
                    <li><label>申请人身份证号码：</label>{{reDataList.idcard}}</li>
                    <li><label>申请人联系方式：</label>{{reDataList.mobile}}</li>
                    <li><label>申请借款金额：</label><span v-if="reDataList.loanmoney">{{fMoney(reDataList.loanmoney)}}元</span></li>
                    <li><label>申请日期：</label>{{fDate(reDataList.loanapplytime,'yyyy-MM-dd')}}</li>
                    <li><label>借款期限：</label><span v-if="reDataList.loanapplytime">{{reDataList.loanday}}天</span></li>
                    <li><label>本息支付方式：</label>{{getDictValue(reDataList.paymentmethod, dict.paymentmethod)}}</li>
                    <li><label>借款用途：</label>{{getDictValue(reDataList.loanuse, dict.loanuse)}}</li>
                    <li><label>借款申请须知：</label></li>
                    <p>请借款人注意，根据您的借款申请，借款人本笔借款的借款日利率为【<span v-if="reDataList.dailyrate">{{reDataList.dailyrate*100}}%</span>】；到期还款日为自借款发放之日起第【{{reDataList.loanday}}】日；借款人应还总额为【
                        <span v-if="reDataList.loanmoney">{{fMoney(reDataList.creditsum)}}</span>】元。</p>
                    <p>借款人点击确认后，即表示在借款标满后借款人无条件同意与出借人就上述借款内容按附件所附《借款协议》模板签署《借款协议》，并不可撤销地同意接受该协议约束。</p>
                    <p>借款人有权通过客服申请查看其已成立并生效的《借款协议》。</p>
                </ul>
                <div class="m-line"></div>
                <div class="m-title m-tc">借款协议<span>附件：《借款协议》</span></div>
                <div class="col-md-12">
                    <div class="col-md-6">合同编号：{{reDataList.sno}}</div>
                    <div class="col-md-6">签订日期：{{fDate(reDataList.signtime,'yyyy-MM-dd')}}</div>
                </div>
                <div class="col-md-12">
                    <div class="col-md-6">出借人：详见本协议第一条</div>
                    <div class="col-md-6"></div>
                </div>
                <div class="col-md-12">
                    <div class="col-md-6">借款人：{{reDataList.uname}}</div>
                    <div class="col-md-6">身份证号：{{reDataList.idcard}}</div>
                </div>
                <div class="col-md-12">
                    <div class="col-md-6">居间服务人：微贷(杭州)金融信息服务有限公司</div>
                    <div class="col-md-6"></div>
                </div>
                <div class="mt10">鉴于：</div>
                <p>1、居间服务人是一家在中国杭州合法成立并有效存续的有限公司，拥有并运营微贷网（www.weidai.com.cn）及相关移动终端APP应用平台（包含但不限于随想贷），向网站注册用户提供信息搜集、信息公布、信息交互、借贷撮合等居间服务。本协议中所称“微贷网”均指该网站及相关移动终端APP应用平台，或/和居间服务人。</p>
                <p>2、出借人和借款人均已阅读并同意遵守微贷网的《注册协议》，注册成为微贷网随想贷的用户，并认可微贷网通过网站公开发布的关于注册用户的各种规则。</p>
                <p>3、借款人已获得居间服务人授予的信用额度，约定居间服务人有权在授信额度和期限内为借款人向出借人申请借款提供居间服务。</p>
                <p>4、出借人和借款人同意通过居间服务人的服务、以电子合同的形式达成本借款协议，本借款协议的内容经双方充分阅读且知晓每一条款的含义，是双方真实意思表示。</p>
                <p>借款人与各出借人，通过微贷网的居间服务，就有关借款事项达成如下协议：</p>
                <h5>1、借款详情如下表所示:</h5>
                <ul>
                    <li>借款人信息:</li>
                    <li><label>借款人姓名: </label> {{reDataList.uname}}</li>
                    <li><label>借款金额: </label><span v-if="reDataList.loanmoney"> {{fMoney(reDataList.loanmoney)}}元</span></li>
                    <li><label>借款期限：</label><span v-if="reDataList.loanapplytime"> {{reDataList.loanday}}天</span></li>
                    <li><label>借款年利率: </label> <span v-if="reDataList.annualrate">{{reDataList.annualrate*100}}%</span></li>
                    <li><label>起息日: </label> 以贷款发放日为准</li>
                    <li><label>借款人本息支付方式: </label> {{getDictValue(reDataList.paymentmethod, dict.paymentmethod)}}</li>
                    <li><label>出借人信息（以微贷网中标出借人为准）：</label></li>
                </ul>
                <table class="table table-bordered table-striped mt10">
                    <thead>
                        <tr>
                            <th>出借人姓名</th>
                            <th>出借人ID</th>
                            <th>出借金额</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td colspan="3" class="m-tc">请以实际内容为准！</td>
                        </tr>
                    </tbody>
                </table>
                <h5>2、还款</h5>
                <p>2.1借款人承诺应按照本协议第一条约定的时间和金额按期足额向出借人偿还本息，借款利率根据借款时间进行计息，借款月利率为借款年利率/12，借款日利率为借款年利率/360。</p>
                <p>2.2借款人应还本息总额计算公式为：借款本金+借款利息（借款利息=借款本金×借款利率×借款时间）。借款人根据本息收取/支付方式的不同分为如下三种，借款人根据本协议第一条的约定方式，根据相应的计算公式确定应还本息：</p>
                <p>2.2.1 若借款人本息支付方式采用“一次性还本付息”方式的，借款人应于每笔借款到期日一次性支付本息，即X=借款本金+借款利息。</p>
                <p>2.2.2若出借人本息收取方式采用“等额本息”方式的，借款人对应的每一期应还本息 X=(Aβ(1+β)^m)/((1+β)^m-1)（借款本金为A，借款月利率为β，借款期限为m个月）。</p>
                <p>2.2.3 若出借人本息收取方式采用“按月付息、到期还本”方式的，借款人对应的每一期应还利息=借款本金×借款年利率/12，本金应在最后一期连同当期利息一并支付。</p>
                <p>（注：一个月为一期）</p>
                <H5>3、借款、还款的支付方式</h5>
                <P>3.1出借人在同意向借款人出借相应款项时,已委托微贷网在本借款协议生效后将该笔借款直接划付至借款人帐户。借款人已委托微贷网将还款直接划付至出借人帐户。出借人及借款人均授权微贷网委托银行从出借人或借款人账户中划付应支付或应偿还的借款本息及其他应付费用。
                </p>
                <P>3.2微贷网网站通过公告或其他各种方式明示出借人应享有的各种收益（包括但不限于利息、奖励等），均已包含在本协议的借款利息中。</p>
                <P>3.3借款人和出借人均同意，微贷网接受借款人或出借人委托所采取的划付款项行为，所产生的法律后果均由相应委托方借款人或出借人自行承担。</p>
                <P>3.4因网络系统原因，实际放款金额与应发金额（即按本合同应发放的贷款金额）将存在微小误差，借款人同意按如下方式处理：</p>
                <P>3.4.1 若实际放款金额＜应发金额，则借款金额以应发金额为准，借款人自愿承担该误差；</p>
                <P>3.4.2 若实际放款金额＞应发金额，则借款人需返还差额，返还方式以微贷网通知为准。</p>
                <P>3.5借款人同意，起息日为其收到微贷网发出的借款成功之日，止息日为出借人收到出借本金及利息之日。因款项划付需要，借款人允许自微贷网发出借款成功提醒至借款人收到该笔借款、借款人还款自银行划付至出借人收到该笔款项之间存留48小时的时间差，在该合理的时间差内，借款人自愿承担相应的利息。</p>
                <P>3.6本协议的签订地和借款还款的履行地均为：浙江省杭州市江干区。 </p>
                <h5>4、逾期还款</h5>

                <p>4.1出现下列逾期还款情形时，借款人除向出借人支付正常费用以外,还应支付逾期违约费用，其计算公式为：逾期费用=逾期金额（本金+利息）<em>逾期费率（日）</em>逾期天数，逾期费率为8‰/天：</p>

                <p>4.1.1应还款日未偿还或未足额偿还的；</p>

                <p>4.1.2应提前还款导致还款计划变更，借款人未足额偿还的；</p>

                <p>4.1.3因借款人违约，微贷网终止向借款人提供服务并宣布借款提前到期，借款人未偿还或为足额偿还的。 借款人发生上述逾期行为的，微贷网有权委托第三方上门催收，因催收产生的费用由客户承担，具体费用标准为根据第三方委派的催收人数按每人500元/次收取。上述所有各类费用，借款人和出借人均同意由微贷网或债权受让方（如有）收取。
                </p>

                <p>4.2借款人同意，其逾期还款造成出借人因此支付的费用(包括但不限于律师费)由借款人承担；</p>

                <p>4.3借款人逾期的，出借人及借款人同意并支持微贷网采取以下措施之一项或几项：</p>

                <p>4.3.1将借款人的有关身份资料及其他个人信息通过微贷网“逾期黑名单”等栏目对外公开；</p>

                <p>4.3.2将借款人的有关身份资料及其他个人信息正式备案在“不良信用记录”,列入全国个人信用评级体系的黑名单(“不良信用记录”数据将为银行、电信、担保公司、人才中心等有关机构提供个人不良信用信息)；</p>

                <p>4.3.3对借款人采取法律措施,由此所产生的所有法律后果将由借款人来承担；</p>

                <p>4.3.4提前解除借款协议并要求借款人偿还全部借款本息。</p>

                <h5>5、提前还款</h5>

                <p>双方同意,借款人选择一次性还本付息方式的，有权提前清偿全部或者部分借款，但需支付所有利息。</p>

                <h5>6、借款债权的转让</h5>

                <p>6.1借款人知晓并同意出借人于本协议履行过程中可能将其因本借款协议而享有的债权的全部或部分转让给不特定的第三人，且转让次数无限制。</p>

                <p>6.2出借人将其债权全部或部分转让给第三人的，应当通知借款人，可以以信件、电子邮件、短信、电话或当面送达等合法形式。该债权转让自出借人或其委托人自通知送达之日起对借款人发生效力。</p>

                <p>6.3就债权转让事宜，出借人在此无条件且不可撤销的授予微贷网如下委托事项及权限：</p>

                <p>6.3.1在符合本协议6.7条或本协议其他条款约定或微贷网公告的债权转让的情形发生时，由微贷网代表全体出借人与债权受让人（或与债权受让人、借款人一并）签订《债权转让协议》和/或《债权转让确认书》及其他债权转让有关的合同、文件；</p>

                <p>6.3.2将债权转让事宜（包括债权受让人等）通知借款人；</p>

                <p>6.3.3代收债权转让对价款项； 上述委托的有效期自本协议生效之日起直至本协议项下全部借款本息及清偿费用全部清偿完毕之日止。
                </p>

                <p>6.4借款人在此已明确知晓微贷网的受托权限，承诺在接到微贷网的债权转让通知后即对债权受让人负有对应债权的清偿义务，而无须微贷网再行出具出借人的书面委托。</p>

                <p>6.5出借人将其债权全部或部分转让给第三人的，本借款协议中对应权利及义务一并转让给债权受让人，包括但不限于主张罚息、利息、单方提前解除合同等权利及支付中介服务费用的义务。</p>

                <p>6.6在借款人正常还款的情形下，出借人可按照微贷网公布的债权转让规则将债权转让给第三人。</p>

                <p>6.7出借人和借款人均同意，为集中维护各出借人权利，如出现微贷网认为有需要的（包括但不限于借款人未能按期如数清偿任意一期借款本息或微贷网认为借款人有逾期风险等）情形下，全体出借人一致同意将本协议项下债权转让给微贷网居间介绍的其他愿意受让该项债权的第三人。出借人在此授权微贷网：上述情形发生之后，由微贷网作为全体出借人的代理人，代为选择债权受让人；代为与债权受让人签订债权转让涉及的所有合同、文件；代为向借款人发送债权转让通知；代为决定或进行其他与债权转让有关的事项，包括但不限于视实际情况独立决定债权转让的价格、支付债权转让对价的时间和方式等。自债权转让通知送达至借款人时，债权受让人取代出借人成为本借款协议新的债权人，取得与债权有关的所有权利。</p>

                <h5>7、微贷网的收费</h5>

                <p>7.1账户管理费是指因居间服务人为借款人与出借人提供交易信息、信用咨询、信用评估、还款提醒、账户管理、还款特殊情况沟通、债权转让各项委托等系列相关服务而由借款人支付给微贷网的报酬。</p>

                <p>7.2借款人同意在借款成功后向微贷网支付账户管理费，账户管理费的计算公式如下：借款人应向微贷网支付的账户管理费总额=借款人应付款总额-出借人应收本息总额。</p>

                <p>7.2.1 若借款人本息支付方式采用“一次性还本付息”方式的，借款人应向微贷网支付的账户管理费总额=借款本金<em>借款期限</em>日管理费率。</p>

                <p>7.2.2 若出借人本息收取方式采用“等额本息”方式的，借款人当期应付款项=借款本金/借款期数+借款本金×综合年费率/12，</p>

                <p>7.2.3若出借人本息收取方式采用“按月付息、到期还本”方式的，借款人当期应付款项=借款本金×综合年费率/12，本金应在最后一期连同当期利息及其他费用一并支付。 费用详情如下表：</p>
                <table class="table table-bordered table-striped mt10">
                   <thead>
                    <tr>
                        <th>借款人姓名</th>
                        <th>账户管理费</th>
                    </tr>
                   </thead>
                   <tbody>
                    <tr>
                        <td></td>
                        <td></td>
                    </tr>
                   </tbody>
                </table>

                <p>7.3借款人同意在借款成功后向微贷网支付账户管理费，并同意微贷网从借款本金中直接扣除。借款人人同意，微贷网应收取的账户管理费独立于出借人应收取的借款本息，但为简化借款人分别支付本息及账户管理费的繁琐程度，微贷网有权在收到借款人支付的总款项中直接收取对应的账户管理费。</p>

                <p>7.4如借款人通过其他第三方提供的中介服务而签订本借款协议的，或借款人与第三方签订了与本借款协议有关的微贷网认可的其他服务协议的，借款人委托微贷网在借款成功时从借款本金中直接扣除借款人应支付的相关费用，由微贷网代为和第三方结算。</p>

                <p>7.5包括上述费用在内的各种收费及标准，微贷网可事先在官方网站公告，出借人和借款人在签订本协议前均对此完全知悉并同意以微贷网公告为准。
                </p>

                <p>
                    <u>为避免不必要的歧义，出借人与借款人一致同意：本借款协议及附属文件项下的借款人应还本息(即出借人应收本息)是指借款人应向出借人偿还的本金和利息，并不包含出借人应向微贷网支付的居间服务费、账户管理费及其他费用（如有）。借款人应付款总额是指借款人基于本借款协议及其他协议应向出借人支付的本息总额以及应向微贷网支付的账户管理费及其他费用（如有）的总和。</u>
                </p>

                <h5>8、违约责任</h5>

                <p>8.1 借款人出现以下情形之一的，即构成违约：</p>

                <p>8.1.1 提供虚假信息；</p>

                <p>8.1.2 擅自变更借款用途；</p>

                <p>8.1.3 逾期还款；</p>

                <p>8.1.4 转移资产，逃避债务。 借款人出现上述情形时，出借人、微贷网有权要求借款人及时纠正，并有权采取其他有效的措施防止违约后果的发生或继续扩大。
                </p>

                <p>8.2 本协议各方均应严格履行协议义务，任何一方违约，违约方应承担因违约使其他各方产生的费用和损失，包括但不限于调查费、诉讼费、律师费等。</p>

                <p>8.3出借人和借款人双方同意,若借款人出现如下任何一种情况,则本协议项下的全部借款自动提前到期,借款人在收到微贷网发出的借款提前到期或解除合同等通知后，应立即清偿全部本金、利息、逾期利息及根据本协议产生的其他全部费用：
                    8.3.1借款人因任何原因逾期支付还款的 ； 8.3.2借款人的工作单位、职务、婚姻状况、健康状况、固定家庭财产或住所变更后，未在30天内通知微贷网的； 8.3.3借款人发生影响其清偿本协议项下借款的其他不利变化，未在7天内通知微贷网的；
                </p>

                <p>8.3.4 出借人有理由相信借款人不再具备还款能力或可能发生严重影响其还款能力的。</p>

                <p>8.4借款人的每期还款均应按照如下顺序清偿：</p>

                <p>8.4.1根据本协议产生的除本款8.3.2--8.3.6项之外的其他全部费用（包括但不限于平台收取的账户管理费）；</p>

                <p>8.4.2违约金；</p>

                <p>8.4.3拖欠的利息；</p>

                <p>8.4.4拖欠的本金；</p>

                <p>8.4.5正常的利息；</p>

                <p>8.4.6正常的本金。</p>

                <h5>9、法律适用和管辖</h5>

                <p>本协议的签订、履行、终止、解释均适用中华人民共和国法律,相关纠纷均由约定的合同签订地和履行地所属杭州市江干区人民法院管辖。</p>

                <h5>10、特别条款</h5>

                <p>10.1出借人保证，用于出借的资金是合法取得的资金。同时借款人保证，借款人将借款用于合法用途，不将所借款项用于任何违法活动(包括但不限于赌博、吸毒、贩毒、卖淫嫖娼等)及一切高风险投资(如证券期货、彩票等)。如借款人违反前述保证或有违反前述保证的嫌疑，则出借人有权采取下列措施：
                </p>

                <p>10.1.1宣布提前收回全部借款；</p>

                <p>10.1.2出借人向公安等有关行政机关举报,追回此款并追究借款人的刑事责任，出借人和借款人均同意并授权微贷网采取前述措施。</p>

                <p>10.2 借款人保证并承诺：</p>

                <p>10.2.1本人在本借款协议项下的借款成功后，在微贷网平台的借款余额不超过人民币20万元；在包括微贷网在内的不同网络借贷信息中介机构平台的借款总余额不超过人民币100万元。</p>

                <p>10.3借款人和出借人均不得利用微贷网平台进行信用卡套现和其他洗钱等不正当交易行为,若有发现，微贷网有权向公安等有关行政机关举报,追究其相关法律责任。</p>

                <p>10.4 借款人以各种方式提供的如下联络通讯方式以供接收与本协议有关的各项通知（包括但不限于还款通知、债权转让通知等）：</p>

                <p>10.4.1移动电话（如有），其中短信方式发送的短信成功发出即为有效送达；</p>

                <p>10.4.2 QQ（如有），QQ消息发送成功时即为有效送达；</p>

                <p>10.4.3电子邮箱（如有），邮件发送成功时即为有效送达；</p>

                <p>10.4.4微贷网提供的借款人站内短信，站内短信发送成功时即为有效送达；</p>

                <p>10.4.5邮件快递：以借款人身份证（或营业执照）地址为准，收寄后三个工作日或邮件签收两者之间先到者即为有效送达；</p>

                <p>10.4.6当面送达的，以借款人签收的书面文件中落款标明的日期为有效送达日期。 借款人认可上述联络通讯方式中任意一种接收到通知即对借款人产生法律效力。
                </p>

                <h5>11、其他</h5>

                <p>11.1 免责声明。您将对您发布的信息及其他在微贷网平台上发生的任何行为承担责任，我公司对此不负任何责任；微贷网作为信息中介平台，并不作为接受服务方或是提供服务方的身份参与交易行为，微贷网提醒用户应该通过自己的谨慎判断确定服务及相关信息的真实性、合法性和有效性。</p>

                <p>11.2.不可抗力。对于因微贷网合理控制范围以外的原因，包括但不限于自然灾害、罢工或骚乱、物质短缺或定量配给、暴动、战争行为、政府行为、通讯或其他设施故障或严重伤亡事故等，致使微贷网延迟或未能履约的，微贷网不对您承担任何责任。</p>

                <p>11.3本协议采用电子文本形式制成,以网络点击的方式在微贷网签订，出借人、借款人均委托微贷网保管本协议。</p>

                <p>11.4本协议自借款人在微贷网发布的借款标的审核成功且借款标满之日（即本协议题头标明的签订日）起生效。</p>

                <p>11.5借款人、出借人在履行本协议过程中，应遵守微贷网在网站上公开发布的在法律允许范围内的各项规则规定。</p>

                <p>11.6出借人、借款人一致同意、授权或认可，微贷网作为网络借款的中介平台根据本协议的规定、委托和微贷网的其他规定行使各项权利、发出各项通知或采取各项措施，一切法律后果和风险均由借款人或出借人承担。</p>

            </div>
        </div>
    </div>
    <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 合同模板
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


var queryProd = action.rootPath + action.interface.queryById
    export default {
        props: ['modalProps']
        ,data () {
            return {'reDataList':[],'dict':dict}
        }
        ,components: {}

        ,created () {
            let vm = this
            let id = vm.modalProps.id;
            if(id){
                //请求详情内容
                vm.send(queryProd, {'id': vm.modalProps.id}, function (res) {
                if (res.errorcode == 0) {
                    vm.reDataList = res.result
                }
                })                   
            }
            
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
        }           
        ,methods: {
            close () {
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


