<template>
    <div>
        <button type="button" class="btn btn-default btn-outline" @click="goBack">返回</button>
        <div class="infoTab">
            <div class="title col-xs-12">还款记录</div>
            <div class="col-xs-12 info clearfix">
                <div class="col-xs-4">
                    <p>
                        <label>客户姓名：</label>
                        <span>{{tableData.uname}}</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>身份证号：</label>
                        <span>{{tableData.idcard}}</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>贷款编号：</label>
                        <span>{{tableData.loanno}}</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>申请金额：</label>
                        <span>{{fMoney(tableData.loanmoney)}} 元</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>到账金额：</label>
                        <span>{{fMoney(tableData.accountmoney)}} 元</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>管理费：</label>
                        <span>{{fMoney(tableData.mngmoney)}} 元</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>贷款利率：</label>
                        <span v-if="tableData.dailyrate">{{tableData.dailyrate*100}}%</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>贷款期限：</label>
                        <span>{{tableData.loanday}} {{getDictValue(tableData.termtype, dict.termType)}}</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>贷款开始日：</label>
                        <span>{{fDate(tableData.loantime, 'yyyy-MM-dd')}}</span>
                    </p>
                </div>
                <div class="col-xs-4">
                    <p>
                        <label>贷款到期日：</label>
                        <span>{{fDate(tableData.endtime, 'yyyy-MM-dd')}}</span>
                    </p>
                </div>
            </div>
        </div>
        <div class="infoTab">
            <div class="mt10">
                <table class="table table-bordered table-striped">
                    <thead>
                        <tr>
                            <th>还款时间</th>
                            <th>还款期数</th>
                            <th>归还金额（元）</th>
                            <th>归还本金（元）</th>
                            <th>归还利息（元）</th>
                            <th>归还罚息（元）</th>
                            <th>还款类型</th>
                            <th>状态</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in tableData.repayrecordinfos">
                            <td>{{fDate(item.rearepaytime, 'yyyy-MM-dd')}}</td>
                            <td>{{item.termnum}}</td>
                            <td>{{fMoney(item.payedmoney)}}</td>
                            <td>{{fMoney(item.payedcapital)}}</td>
                            <td>{{fMoney(item.payedinterest)}}</td>
                            <td>{{fMoney(item.payedpenaltymoney)}}</td>
                            <td>{{getDictValue(item.repaytype, dict.repaytype)}}</td>
                            <td>{{getDictValue(item.status, dict.loanStatus)}}</td>
                        </tr>
                    </tbody>
                </table>
                <!--暂无数据 start-->
                <div class="noData mt10" style="display:block;text-align:center;">
                    {{reResult}}
                </div>
                <!--暂无数据 end-->
            </div>
        </div>
    </div>
</template>

<script>
import action from '../../action/interface.js'
import mixin from '../mixin.js'
import dict from '../../js/dict.js'

    export default {
        data () {
            //console.log(111)
            return {'loanNo': '','tableData': [], 'reResult': '','dict':dict}
        }
        ,created: function() {
        //eventHub.$on('add-tab')
        var id = this.$route.query.id;
        console.log(id);
        //console.log(id);
        //aParams.id = id;
        //console.log(aParams.id);
        }
      ,methods: {
         goBack () { //返回
            this.$router.go(-1)
        }
        ,renderTable () {
            let vm = this;
            let aUrl = action.rootPath + action.interface.repayRecord;
            let aParams = {'loanNo': ''};
            aParams.loanNo = vm.$route.query.loanno;
            vm.send(aUrl, aParams, function (res) {
                if ( res.errorcode == 0 ) {
                    vm.tableData = res.result;
                    if (res.errorcode == 0 && res.result.repayrecordinfos.length <= 0) { //无数据
                        vm.reResult = "暂无数据"
                    }
                }else { //返回异常信息
                        vm.reResult = res.message
                    }
            })
        }
        }
        ,activated () {
           this.renderTable ();
        }
        ,mixins: [mixin]
    }
</script>


