<style>
    .lwm-list {
        padding: 0;
    }
    .lwm-list li{
        height: 40px;
        line-height: 40px;
        border-bottom:1px #ccc solid; 
        list-style: none;
    }
</style>
<template>
    <div class="modal-content">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" @click="close"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">周围前5位wifi的MAC地址</h4>
        </div>
        <div class="modal-body">
            <form action="#" method="post" id="deleteWhite" class="regForm">
                <div class="col-xs-12 form-inline group-left validateModal loan-alert">
                    <ul class="clearfix lwm-list" v-if="modalProps.lwamList">
                        <li v-for="item in modalProps.lwamList">{{item}}</li>
                    </ul>
                    <p v-else class="tc">暂无数据</p>
                </div>
            </form>
            </div>
            <div class="modal-footer">
                 <span>
                    <button type="button" class="btn btn-default" @click="goBack">关闭</button>
                </span>
            </div>
        </div>
        <!-- /.modal-content -->
    </div>
</template>
<script>
/*
 * 周围前5位wifi的MAC地址
 * params: {modalProps: {lwamList: MAC地址列表}}
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

    export default {
        props: ['modalProps']
        ,data () {
            return {'uname': '', 'idcard': '', 'mobile': '', 'remark': '','dict': dict}
        }
        ,created () {
        }
        ,mounted () {
            eventHub.$on('go-back', this.goBack)
        }        
        ,methods: {
            close () {
                eventHub.$emit('close-modal')
            }
            ,goBack () {//返回
                eventHub.$emit('close-modal')
            }
        }
        ,mixins: [mixins]
    }
</script>


