<template>
 <div class="infoTab">
    <div class="title col-xs-12">审核历史</div>
    <div class="infoTable">
      <table class="table table-bordered table-striped">
        <thead>
        <tr>
          <th>序号</th>
          <th>受理岗位</th>
          <th>受理人员</th>
          <th>受理结果</th>
          <th>审核说明</th>
          <th>受理时间</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for='(item, index) in reDataList'>
          <td>{{ index + 1}}</td>
          <td v-if="item.auditType == 1">初审岗</td><td v-else>复审岗</td>
          <td>{{item.operatorName}}</td>
          <td v-if="item.status == 1">通过</td><td v-else>未通过</td>
          <td>{{item.detail}}</td>
          <td>{{timeNormal(item.acceptTime)}}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="noData" style="padding:0">
                {{errorInfo}}
            </div>
  </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
export default {
        props: ["parentId"]
        ,data () {
            return {'reDataList': '','errorInfo': ''}
        }
        ,computed: {
           'qParams': function () {
              return {'parentId':this.parentId}
           }
           ,'url': function () { //请求地址
            return action.rootPath + action.interface.queryLog
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>


