
<template>
    <span>
    <a type="button" class="btn btn-default btn-sm btn-outline mr15" href="javascript:;" @click="doQuery"><i class="icon-search"></i>&nbsp;查询</a>
    <a type="button" class="btn btn-default btn-sm btn-outline" href="javascript:;" @click="doRefresh"><i class="icon-undo"></i>&nbsp;刷新</a>
    </span>
</template>
<script>
/*
 * 查询区按钮
 */
    export default {
        methods: {
            'doQuery': function () {
                eventHub.$emit('do-query');
            }
            ,'doRefresh': function () {
                eventHub.$emit('do-refresh');
            }
        }
    }
</script>


