<template>
    <div class="grey-list box col-xs-12 box-inner clearfix">
        
        
     <button type="button" class="btn btn-default btn-outline" @click="goBack">返回</button>
        <div class="mt10 infoTab">
            <div class="title col-xs-12">还款计划</div>
              <table class="table table-bordered table-striped mt10">
                <thead>
                <tr>
                  <th>日期</th>
                  <th>期数</th>
                  <th>应还本金（元）</th>
                  <th>应还利息（元）</th>
                  <th>应还罚息（元）</th>
                  <th>应还合计（元）</th>
                  <th>状态</th>
                  <th>操作</th>
                </tr>
                </thead>
                <tbody>
               <tr v-for="(item, index) in reDataList">
                <td>{{fDate(item.repaydate, 'yyyy-MM-dd')}}</td>
                <td>{{item.termnum}}</td>
                <td>{{fMoney(item.needcapital)}}</td>
                <td>{{fMoney(item.interestmoney)}}</td>
                <td>{{fMoney(item.needpenaltymoney)}}</td>
                <td>{{fMoney(item.needtotalmoney)}}</td>
                <td>{{getDictValue(item.status, dict.planStatus)}}</td>
                <td><a v-if="$route.query.status == 3 || $route.query.status == 4" href="javascript:;" @click="clearAccount(reDataList[index])">结清贷款</a></td>
              </tr>
                </tbody>
              </table>
              <!--暂无数据 start-->
            <div class="noData">
                {{errorInfo}}
            </div>
          <!--暂无数据 end-->
        </div>
        <page :pageInfo="pageInfo" :total="total"></page>
  </div>

</template>

<script>
/*
 * 还款计划
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import returnMoney from './return-money.vue'
import page from '../basic/page/page.vue'
import footerBtnGroup from '../basic/footer-btn/footer-btn-group.vue'


var pagesize = 8
    export default {
        components: { page, footerBtnGroup,returnMoney}
        ,data () {
            return {'loanNo': '','pageNum': 1, 'total': 0, 'reDataList': [], 'uid': this.$route.query.uid, 'errorInfo': '', 'validdate': '', 'dict': dict}
        }
        ,computed: {
          //分页用
            'pageInfo': function () {
                return {'pageSize': pagesize, 'currentpage': this.pageNum}
            }
            ,'qParams': function () {
                let _offset = this.cptOffset(this.pageNum, pagesize)
                return {'loanNo': this.$route.query.loanno, 'offset': _offset, 'pagesize': pagesize}
            }
            ,'url': function () {
              return action.rootPath + action.interface.repayPlan
            }
        }
        ,activated () {
            this.doQuery()
        //监听查询
        eventHub.$on('do-query', this.clickQuery)
        //监听刷新
        eventHub.$on('do-refresh', this.doQuery)
        //监听跳转第几页
        eventHub.$on('go-page', this.goPage)
        }
        ,methods: {
            //结清贷款
            clearAccount (detail) {
                eventHub.$emit('open-modal', returnMoney, {'detail': detail})
            }
            ,goBack () { //返回
            this.$router.go(-1)
        }}
        ,mixins: [mixins]
    }
</script>


