
<template>
    <div class="infoTab">
        <div class="title col-xs-12">授权更新记录</div>
        <div class="infoTable">
            <button type="button" class="btn btn-default btn-outline" @click="goBack">返回</button>
            <div class="table-responsive">
                <table class="table table-bordered table-striped">
                  <thead>
                  <tr>
                    <th>序号</th>
                    <th>客户姓名</th>
                    <th>客户ID</th>
                    <th>更新时间</th>
                    <th>授权类型</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="(item, index) in reDataList">
                    <td>{{(pageNum - 1) * 8 + index + 1}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.uid}}</td>
                    <td>{{fDate(item.createtime,'yyyy-MM-dd')}}</td>
                    <td>{{getDictValue(item.infotype, dict.infotype)}}</td>
                  </tr>
                  </tbody>
                </table>
            </div>
        </div>
        <page :pageInfo="pageInfo" :total="total"></page>
    </div>
</template>
<script>
/*
 * 授权更新记录查询
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'

import page from '../basic/page/page.vue'
//历史详情查询地址
var qOperateHis = action.rootPath + action.interface.queryCreditTagHistory
var pagesize = 8
export default {
    data () {
        return {'pageNum': 1, 'total': 0, 'reDataList': [], 'errorInfo': '', dict}
    }
    ,components: {page}
    ,computed: {
        //分页用
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
        ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
            return {'offset': _offset, 'pagesize': pagesize, 'uid': this.$route.query.uid}
        }
        , 'url': function () { //请求地址
            return qOperateHis
        }
    }
    ,watch: {
        'uid': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
        }
    }
    ,mounted () {
        this.doQuery()
        //监听跳转第几页
        //eventHub.$on('go-page', this.goPage)
    }
    ,methods: {
        goBack () { //返回
            this.$router.go(-1)
        }
        ,showDetail (pid) {
            eventHub.$emit('open-modal', detailProd, {'pid': pid, 'url': qOperateHis});
        }
    }
    ,mixins: [mixins]
    ,activated () {
        // this.uid = this.$route.query.uid
        this.pageNum = 1;
        this.doQuery()
    }
}
</script>


